import * as React from 'react';
import { LuFileEdit } from "react-icons/lu";
import { IoChevronBack, IoChevronForward } from 'react-icons/io5';
import { AiOutlineInfoCircle, AiOutlineEye, AiOutlineEyeInvisible } from 'react-icons/ai';
import { Typography, Input, Textarea, Popover, PopoverHandler, PopoverContent, Button, Tooltip } from '@material-tailwind/react';
import { format } from 'date-fns';
import { DayPicker } from 'react-day-picker';
import { toast } from 'react-toastify';
import { useLocation } from 'react-router-dom';
import ReactSelect from 'react-select';
import services from '../../../../../utils/services';

type Field = {
  id: string;
  form_name: string;
  field_type: string;
  field_name: string;
  form_type: string;
  max_value: number;
  is_privacy: number;
  is_visible: number;
  is_mandatory: number;
  tab_form_application_id: string;
  class: string;
  sort: number;
  is_other: any;
  notes: string;
  tooltip: string;
  placeholder: string;
  value: string;
  valueView: string;
};

export default function BasicInformation({ data }) {
  const location = useLocation();
  const [isUpdateForm, setIsUpdateForm] = React.useState(false);
  const [isSiblingBI, setIsSiblingBI] = React.useState(true);
  const successNotify = (content) => toast.success(content);
  const errorNotify = (content) => toast.error(content);
  const [isLoading, setIsLoading] = React.useState(false);
  const [loadingButton, setLoadingButton] = React.useState(false);
  const [formData, setFormData] = React.useState({
    isSubmit: false,
    fields: [
      {
        id: '',
        form_name: '',
        field_type: '',
        field_name: '',
        form_type: '',
        max_value: 0,
        is_privacy: 0,
        is_visible: 0,
        is_mandatory: 0,
        tab_form_application_id: '',
        class: '',
        sort: 0,
        is_other: null,
        notes: null,
        tooltip: null,
        placeholder: '',
        tab_name: '',
        value: '',
        valueView: ''
      }
    ],
    privacies: []
  });
  const [options, setOptions] = React.useState({
    genders: [],
    countries: [],
    provinces: [],
    districts: [],
    maritals: [],
    religions: [],
    healthTestSites: [],
    siblings: [],
  });

  const dangerStyles = {
    control: (provided, state) => ({
      ...provided,
      // border: formData.isSubmit && !formData['1'].title ? '2px solid #ef4444' : state.isFocused ? '2px solid #1566D1' : '2px solid #d1d5db',
      borderRadius: '0.375rem', // Adjust border radius as needed
      height: '44px',
      minHeight: '44px',
    }),
    placeholder: (provided) => ({
      ...provided,
      color: '#9ca3af', // Adjust color as needed
    }),
  };

  React.useEffect(() => {
    if (location.pathname.includes(data.code_tab)) isUpdateForm ? fetchData() : fetchForm();
  }, [data, isUpdateForm]);

  const fetchDistrict = async (provinceCode) => {
    const districts = await services.get('/master/region/district/' + provinceCode);
    // reset district
    // setFormData(prevData => ({ ...prevData, fields: prevData.fields.map((item) => item.field_name === 'birthdistrict' ? { ...item, value: null } : item) }))
    setOptions((prevData) => ({ ...prevData, districts: districts.data }));
  };

  const fetchForm = async () => {
    setIsLoading(true);
    try {
      const forms = await services.get('/master/form-application/' + data.id);
      setFormData((prevData) => ({
        ...prevData, fields: forms.data, privacies: forms.data
          .map((item, index) => ({ ...item, index }))  // Tambahkan indeks ke setiap item
          .filter(item => item.is_privacy === 1)       // Filter berdasarkan is_privacy
          .map(item => item.index)                     // Ambil nilai indeks
      }));
    } catch (error) {
      errorNotify(error.response.statusText);
    } finally {
      setIsLoading(false);
    };
  };

  console.log(formData.privacies);

  const fetchData = async () => {
    setIsLoading(true);
    try {
      const forms = await services.get('/master/form-application/' + data.id);
      setFormData((prevData) => ({ ...prevData, fields: forms.data }));
      // get district (chaining dari province)
      forms.data.forEach(item => {
        if (item.field_name === 'birthprovince') { fetchDistrict(item.value) };
      });

      const genders = await services.get('/master/option/' + forms.data[5].id);
      setOptions((prevData) => ({ ...prevData, genders: genders.data }));

      const countries = await services.get('/master/citizenship');
      setOptions((prevData) => ({ ...prevData, countries: countries.data }));

      const provinces = await services.get('/master/region/province');
      setOptions((prevData) => ({ ...prevData, provinces: provinces.data }));

      const maritals = await services.get('/master/option/' + forms.data[12].id);
      setOptions((prevData) => ({ ...prevData, maritals: maritals.data }));

      const religions = await services.get('/master/option/' + forms.data[13].id);
      setOptions((prevData) => ({ ...prevData, religions: religions.data }));

      const healthTestSites = await services.get('/master/option/' + forms.data[14].id);
      setOptions((prevData) => ({ ...prevData, healthTestSites: healthTestSites.data }));

      const siblings = await services.get('/master/option/' + forms.data[17].id);
      setOptions((prevData) => ({ ...prevData, siblings: siblings.data }));
    } catch (error) {
      errorNotify(error.response.statusText);
    } finally {
      setIsLoading(false);
    };
  };

  // Toggle is_privacy value function
  const togglePrivacy = (indices) => {
    setFormData(prevFormData => ({
      ...prevFormData,
      fields: prevFormData.fields.map((field, idx) =>
        indices.includes(idx) ? { ...field, is_privacy: field.is_privacy === 0 ? 1 : 0 } : field
      )
    }));
  };

  const switchOption = (fieldName, value = '') => {
    switch (fieldName) {
      case 'gender':
        return value ? options.genders.find(option => option.value === value) : options.genders;
      case 'citizenship_id':
        return value ? options.countries.find(option => option.value === value) : options.countries;
      case 'birthregion':
        return value ? options.countries.find(option => option.value === value) : options.countries;
      case 'birthprovince':
        return value ? options.provinces.find(option => option.province_code === value) : options.provinces;
      case 'birthdistrict':
        return value ? options.districts.find(option => option.district_code === value) : options.districts;
      case 'marriage_status':
        return value ? options.maritals.find(option => option.value === value) : options.maritals;
      case 'religion':
        return value ? options.religions.find(option => option.value === value) : options.religions;
      case 'location_test':
        return value ? options.healthTestSites.find(option => option.value === value) : options.healthTestSites;
      case 'family_question_1':
        return value ? options.siblings.find(option => option.value === value) : options.siblings;
      default:
        break;
    };
  };

  const forms = (item: Field, idx: number) => {
    switch (item.field_type) {
      case 'input':
        const input = item.form_type === 'date' ?
          <div className={item.class}>
            <label htmlFor={item.field_name} className='flex items-center gap-1'>
              {item.form_name}
              {item.tooltip ? <Tooltip content={item.tooltip}>
                <Button variant="text" size="lg" className="p-0"><AiOutlineInfoCircle className="text-gray-500" /></Button>
              </Tooltip> : null}
              {item.is_mandatory ? <span className="text-danger-500">*</span> : null}
            </label>
            <Popover placement="left-start">
              <PopoverHandler>
                <Input
                  labelProps={{ className: "hidden" }}
                  size="lg"
                  id={item.field_name}
                  type="text"
                  placeholder={item.placeholder}
                  className="!border-gray-300 placeholder:text-gray-400 placeholder:opacity-100 focus:!outline-none focus:!border-primary-500"
                  containerProps={{ className: "min-w-[8px] mt-1" }}
                  onChange={() => null}
                  value={item.value ? format(item.value, 'dd-MM-yyyy') : ''}
                />
              </PopoverHandler>
              <PopoverContent>
                <DayPicker
                  mode="single"
                  selected={new Date(item.value)}
                  onSelect={(date) => setFormData(prevData => ({ ...prevData, fields: prevData.fields.map((item, index) => index === idx ? { ...item, value: String(date) } : item) }))}
                  showOutsideDays
                  className="border-0"
                  classNames={{
                    caption: "flex justify-center py-2 mb-4 relative items-center",
                    caption_label: "text-sm font-medium text-gray-900",
                    nav: "flex items-center",
                    nav_button:
                      "h-6 w-6 bg-transparent hover:bg-blue-gray-50 p-1 rounded-md transition-colors duration-300",
                    nav_button_previous: "absolute left-1.5",
                    nav_button_next: "absolute right-1.5",
                    table: "w-full border-collapse",
                    head_row: "flex font-medium text-gray-900",
                    head_cell: "m-0.5 w-9 font-normal text-sm",
                    row: "flex w-full mt-2",
                    cell: "text-gray-600 rounded-md h-9 w-9 text-center text-sm p-0 m-0.5 relative [&:has([aria-selected].day-range-end)]:rounded-r-md [&:has([aria-selected].day-outside)]:bg-gray-900/20 [&:has([aria-selected].day-outside)]:text-white [&:has([aria-selected])]:bg-gray-900/50 first:[&:has([aria-selected])]:rounded-l-md last:[&:has([aria-selected])]:rounded-r-md focus-within:relative focus-within:z-20",
                    day: "h-9 w-9 p-0 font-normal",
                    day_range_end: "day-range-end",
                    day_selected:
                      "rounded-md bg-gray-900 text-white hover:bg-gray-900 hover:text-white focus:bg-gray-900 focus:text-white",
                    day_today: "rounded-md bg-gray-200 text-gray-900",
                    day_outside:
                      "day-outside text-gray-500 opacity-50 aria-selected:bg-gray-500 aria-selected:text-gray-900 aria-selected:bg-opacity-10",
                    day_disabled: "text-gray-500 opacity-50",
                    day_hidden: "invisible",
                  }}
                  components={{
                    IconLeft: ({ ...props }) => (
                      <IoChevronBack {...props} className="h-4 w-4 stroke-2" />
                    ),
                    IconRight: ({ ...props }) => (
                      <IoChevronForward {...props} className="h-4 w-4 stroke-2" />
                    ),
                  }}
                />
              </PopoverContent>
            </Popover>
          </div> :
          <div className={item.class}>
            <label htmlFor={item.field_name} className='flex items-center gap-1'>
              {item.form_name}
              {item.tooltip ? <Tooltip content={item.tooltip}>
                <Button variant="text" size="lg" className="p-0"><AiOutlineInfoCircle className="text-gray-500" /></Button>
              </Tooltip> : null}
              {item.is_mandatory ? <span className="text-danger-500">*</span> : null}
            </label>
            <Input
              labelProps={{ className: "hidden" }}
              size="lg"
              id={item.field_name}
              name={item.field_name}
              type={item.field_type}
              placeholder={item.placeholder}
              className={`${formData.isSubmit && !item.value ? '!border-danger-500' : '!border-gray-300'} placeholder:text-gray-400 placeholder:opacity-100 focus:!outline-none focus:!border-primary-500 mt-1`}
              onChange={({ target }) => setFormData(prevData => ({ ...prevData, fields: prevData.fields.map((item, index) => index === idx ? { ...item, value: target.value } : item) }))}
              value={item.value}
            />
          </div>
        return input;
      case 'select':
        return (<div className={item.class}>
          <label htmlFor={item.field_name} className='flex items-center gap-1'>
            {item.form_name}
            {item.tooltip ? <Tooltip content={item.tooltip}>
              <Button variant="text" size="lg" className="p-0"><AiOutlineInfoCircle className="text-gray-500" /></Button>
            </Tooltip> : null}
            {item.is_mandatory ? <span className="text-danger-500">*</span> : null}
          </label>
          <ReactSelect
            components={{ IndicatorSeparator: () => null }}
            id={item.field_name}
            placeholder={item.placeholder}
            className="mt-1"
            styles={dangerStyles}
            options={switchOption(item.field_name)}
            onChange={(e) => {
              if (item.field_name === 'birthprovince') { fetchDistrict(e.province_code) }
              setFormData(prevData =>
              ({
                ...prevData, fields: prevData.fields.map((item, index) => index === idx ?
                  {
                    ...item, value: item.field_name === 'birthprovince' ?
                      e.province_code :
                      item.field_name === 'birthdistrict' ?
                        e.district_code :
                        e.value
                  } :
                  item)
              }));
            }}
            value={switchOption(item.field_name, item.value)}
          />
          {/* {(formData.isSubmit && !item.value) && <span className="text-danger-500 mt-1">Vacancy stream is required</span>} */}
        </div>);
      case 'textarea':
        return (<div className={item.class}>
          <label htmlFor={item.field_name} className='flex items-center gap-1'>
            {item.form_name}
            {item.tooltip ? <Tooltip content={item.tooltip}>
              <Button variant="text" size="lg" className="p-0"><AiOutlineInfoCircle className="text-gray-500" /></Button>
            </Tooltip> : null}
            {item.is_mandatory ? <span className="text-danger-500">*</span> : null}
          </label>
          <Textarea
            labelProps={{ className: "hidden" }}
            id={item.field_name}
            name={item.field_name}
            placeholder={item.placeholder}
            className={`${formData.isSubmit && !item.value ? '!border-danger-500' : '!border-gray-300'} placeholder:text-gray-400 placeholder:opacity-100 focus:!outline-none focus:!border-primary-500 mt-1`}
            onChange={({ target }) => setFormData(prevData => ({ ...prevData, fields: prevData.fields.map((item, index) => index === idx ? { ...item, value: target.value } : item) }))}
            value={item.value}
          />
          {/* {(formData.isSubmit && !item.value) && <span className="text-danger-500 mt-1">Vacancy stream is required</span>} */}
        </div>);
      default:
        break;
    };
  };

  const extractData = (fields: Field[]) => {
    return fields.reduce((acc, field) => {
      acc[field.field_name] = field.value;
      return acc;
    }, {} as Record<string, string>);
  };

  const saveFormData = async (event) => {
    event.preventDefault();
    setLoadingButton(true);
    setFormData(prevData => ({ ...prevData, isSubmit: true }));
    const resultData = extractData(formData.fields);
    try {
      const response = await services.post('/personal-form/personal-data', { ...resultData, tab_id: data.id });
      setIsUpdateForm((prevData) => !prevData);
      successNotify(response.message);
    } catch (error) {
      errorNotify(error.response.statusText);
    } finally {
      setLoadingButton(false);
    };
  };

  if (isLoading) {
    return (<div className="flex flex-col gap-2">
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
    </div>);
  };
  return (
    <React.Fragment>
      <div className="flex justify-between items-center">
        <Typography variant="lead" className="font-bold text-black">Basic Information</Typography>
        {!isUpdateForm ? <div className="flex items-center gap-1">
          {formData.fields[0].is_privacy ?
            <AiOutlineEyeInvisible size={20} className="cursor-pointer" onClick={() => togglePrivacy(formData.privacies)} /> :
            <AiOutlineEye size={20} className="cursor-pointer" onClick={() => togglePrivacy(formData.privacies)} />} |
          <LuFileEdit size={15} className="cursor-pointer" onClick={() => setIsUpdateForm((prevData) => !prevData)} />
        </div> : null}
      </div>
      <hr className="my-5" />
      {!isUpdateForm ? <div className="grid grid-cols-12 gap-5">
        {formData.fields.map((item, idx) =>
          <div key={idx} className={item.class}>
            <Typography variant="small" className="font-bold text-[#002759] mb-1.5">{item.form_name}</Typography>
            <Typography variant="small">{item.is_privacy ? '********' : item.valueView}</Typography>
          </div>
        )}
      </div> :
        <form onSubmit={saveFormData}>
          <div className="grid grid-cols-12 gap-5">
            {formData.fields.map((item, idx) =>
              <React.Fragment key={idx}>
                {forms(item, idx)}
              </React.Fragment>
            )}
          </div>
          <div className="flex justify-end items-center gap-2 mt-5">
            <Button variant="outlined" className="border-primary-500 text-primary-500" onClick={() => setIsUpdateForm((prevData) => !prevData)}>Cancel</Button>
            <Button loading={loadingButton} type="submit" variant="filled" className="bg-primary-500">Save Form</Button>
          </div>
        </form>}
    </React.Fragment>
  );
};