import * as React from 'react';
import { useState, useEffect } from 'react';
import { Typography, Button, IconButton, Input, Option, Popover, PopoverHandler, PopoverContent } from '@material-tailwind/react';
import { format } from "date-fns";
import { DayPicker } from "react-day-picker";
import Select from 'react-select';
import { toast } from 'react-toastify';
import services from '../../../../../utils/services';
import { IoIosCloseCircleOutline } from 'react-icons/io';
import { IoChevronBack, IoChevronForward } from "react-icons/io5";

type T_Skill = {
  id: string;
  name: string;
};

type T_Option = {
  label: string,
  value: string,
}

type T_Field = {
  id: string | null,
  form_name: string | null,
  field_type: 'input' | 'select',
  field_name: string | null,
  form_type: 'text' | 'number' | 'date',
  max_value: number | null,
  is_visible: boolean,
  is_mandatory: boolean,
  tab_form_application_id: string,
  class: string | null,
  sort: number,
  is_other: boolean | null,
  notes: string | null,
  tooltip: string | null,
  placeholder: string | null,
  tab_name: string | null,
  is_privacy: boolean | null,
  max_upload: number | null,
  mimes: string | null,
  options: T_Option[] | null,
}

export default function Skill() {
  const [formDisabled, setFormDisabled] = useState<boolean>(false);
  const [skillList, setSkillList] = useState<T_Skill[]>([]);
  const [personalSkillList, setPersonalSkillList] = useState([]);
  const [field, setField] = useState<T_Field>(null);
  const [selectedData, setSelectedData] = useState<T_Option>(null);
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    getSkillDataList();
    getPersonalSkillDataList();
  }, [])

  useEffect(() => {
    getFormFields();
  }, [])

  const getFormFields = async () => {
    try {
      const response = await services.get('/master/tab-form');

      if(response.statusCode === 200) {
        const form = response.data.find((item) => item.name === 'Skill');
        const response2 = await services.get(`/master/form-application/${form.id}`);

        if(response2.statusCode === 200) {
          setField(response2.data[0]);
          // console.log(response2.data);
        }
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to get master skill data');
    }
  }

  const getSkillDataList = async () => {
    try {
      setLoading(true);
      const response = await services.get('/personal-form/personal-skill/master_skill');
  
      if(response.statusCode === 200) {
        setSkillList(response.data);
        setLoading(false);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to get master skill data');
    }
  }

  const getPersonalSkillDataList = async () => {
    try {
      setLoading(true);
      const response = await services.get('/personal-form/personal-skill');
  
      if(response.statusCode === 200) {
        setPersonalSkillList(response.data);
        setLoading(false);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to get personal skill data');
    }
  }

  const onAddSkill = async () => {
    try {
      setLoading(true);
      const response = await services.post('/personal-form/personal-skill/save', {
        skill_id: selectedData.value,
      });

      if(response.statusCode = 201) {
        toast.success('Skill added successfully');
        setSelectedData(null);
        getPersonalSkillDataList();
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to add skill');
    }
  }

  const onDeletePersonalSkill = async (id: string) => {
    try {
      setLoading(true);
      const response = await services.delete(`/personal-form/personal-skill/delete/${id}`);

      if(response.statusCode === 200) {
        toast.success('Personal skill deleted successfully');
        getPersonalSkillDataList();
        setLoading(false);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to delete personal skill');
    }
  }

  return (
    <>
      <div className='flex flex-col h-full gap-y-5 bg-white rounded-r-lg'>
        <Typography variant="lead" className="font-bold text-black">Skill</Typography>
        <hr/>
        <div className='flex gap-x-6 items-center'>
          <Select
            placeholder={field?.placeholder}
            className='w-full'
            options={
              [...skillList]
              .filter((item1) => !personalSkillList.find((item2) => item2.skill_id === item1.id))
              .map((item) => ({ value: item.id, label: item.name }))
            }
            components={{
              IndicatorSeparator: () => null
            }}
            value={selectedData}
            onChange={(e) => setSelectedData(e)}
            isDisabled={loading}
          />
          <Button
            variant="filled"
            color="blue"
            className="bg-primary-500 sm:text-nowrap normal-case"
            disabled={loading || !selectedData}
            loading={loading}
            onClick={onAddSkill}
          >
            Add Skill
          </Button>
        </div>
        {loading 
          ? <>
              <hr className='border-gray-400'/>
              <div className='flex flex-wrap gap-[10px] items-center'>
                <div className='h-[43px] w-[150px] bg-gray-200 rounded-md animate-pulse'/>
                <div className='h-[43px] w-[110px] bg-gray-200 rounded-md animate-pulse'/>
                <div className='h-[43px] w-[190px] bg-gray-200 rounded-md animate-pulse'/>
                <div className='h-[43px] w-[70px] bg-gray-200 rounded-md animate-pulse'/>
                <div className='h-[43px] w-[130px] bg-gray-200 rounded-md animate-pulse'/>
              </div>
            </>
          : personalSkillList.length > 0 && (
            <>
              <hr className='border-gray-400'/>
              <div className='flex flex-wrap gap-[10px] items-center'>
                {personalSkillList.map((item, i) => (
                  <div key={item.id} className='flex py-[10px] px-[14px] gap-x-[5px] border border-primary-500 bg-primary-50/50 rounded-md items-center'>
                    <span>{item.name}</span>
                    <IconButton
                      variant="text"
                      color="red"
                      className="p-0 w-5 h-5 hover:bg-transparent hover:text-red-700"
                      disabled={loading}
                    >
                      <IoIosCloseCircleOutline
                        size={20}
                        className='text-gray-400 hover:text-red-700'
                        onClick={() => onDeletePersonalSkill(item.id)}
                      />
                    </IconButton>
                  </div>
                ))}
              </div>
            </>
        )}
      </div>
    </>
  );
};