___CSS_LOADER_EXPORT___.push([module.id, `.progress {
    list-style: none;
    font-size: 12pt;
    padding-left: 5px;
}
.progress li {
    /* border-left: 1px dashed #0061E3; */
    padding: 0 0 0.1em 2em;
    position: relative;
}
.progress li::before {
    content: "";
    border-radius: 50%;
    border: 1.15px solid #0061E3;
    background-color: #EBF3FD;
    width: 15.2px;
    height: 15.2px;
    position: absolute;
    left: -0.21em;
    top: 0.25em;
}
.progress.work-experience li::after {
    content: '';
    position: absolute;
    height: 90%;
    border: 0.5px dashed #0061E3;
    top: 1.75em;
    left: 0.175em;
}
.progress.org-experience li::after {
    content: '';
    position: absolute;
    height: 80%;
    border: 0.5px dashed #0061E3;
    top: 1.75em;
    left: 0.175em;
}
.progress li:last-child::after{
    content: none;
