import * as React from 'react';
import { Typography, Button } from '@material-tailwind/react';
import { toast } from 'react-toastify';
import { AiOutlineEye } from 'react-icons/ai';
import { RiDeleteBinLine } from "react-icons/ri";
import { useLocation } from 'react-router-dom';
import services from '../../../../../utils/services';
import jpgIcon from '../../assets/images/jpg-icon.png';
import pdfIcon from '../../assets/images/pdf-icon.png';

export default function SupportingDocument({ data }) {
  const location = useLocation();
  const successNotify = (content) => toast.success(content);
  const errorNotify = (content) => toast.error(content);
  const [isLoading, setIsLoading] = React.useState(false);
  const [loadingButton, setLoadingButton] = React.useState(false);
  const [formData, setFormData] = React.useState({ fields: [] });
  const [formId, setFormId] = React.useState('');
  const [file, setFile] = React.useState<File | null>(null);

  React.useEffect(() => {
    if (location.pathname.includes(data.code_tab)) fetchData();
  }, [data]);

  const fetchData = async () => {
    setIsLoading(true);
    try {
      const forms = await services.get('/master/form-application/' + data.id);
      setFormData((prevData) => ({ ...prevData, fields: forms.data }));
      setIsLoading(false);
    } catch (error) {
      errorNotify(error.response.statusText);
      setIsLoading(false)
    };
  };

  const inputFile = async (e: React.ChangeEvent<HTMLInputElement>, formId: string) => {
    setFormId(formId);
    if (e.target.files && e.target.files.length > 0) {
      const selectedFile = e.target.files[0];
      setFile(selectedFile);
    } else {
      setFile(null);
    };
  };

  const previewDocument = (type, base64Data) => {
    const newTab = window.open();
    if (newTab) {
      if (type === 'pdf') {
        newTab.document.body.innerHTML = `
        <object data="data:application/pdf;base64,${base64Data}" type="application/pdf" width="100%" height="100%">
          <p>PDF viewer tidak didukung di browser ini. Anda bisa <a href="data:application/pdf;base64,${base64Data}">unduh PDF</a> untuk melihatnya.</p>
        </object>
      `;
      } else {
        const css = `
          body, html {
            margin: 0;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
          }
          .background {
            width: 100%;
            height: 100%;
            background-image: url('data:image/jpg;base64,${base64Data}');
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;
          }
        `;
        newTab.document.write(`
          <style>${css}</style>
          <div class="background"></div>
        `);
      };
    };
  };

  const deleteDocument = async (formId: string) => {
    setLoadingButton(true);
    try {
      const response = await services.delete('/personal-form/personal-documents/' + formId);
      fetchData();
      successNotify(response.message);
    } catch (error) {
      errorNotify(error.response.statusText);
    } finally {
      setLoadingButton(false);
    };
  };

  const submitDocument = async (formId: string) => {
    setFormId(formId);
    setLoadingButton(true);
    const formData = new FormData();
    formData.append('file', file);
    formData.append('tab_id', data.id);
    try {
      const response = await services.post('/personal-form/personal-documents/' + formId, formData,
        { headers: { 'Content-Type': 'multipart/form-data' } });
      fetchData();
      successNotify(response.message);
    } catch (error) {
      errorNotify(error.response.statusText);
    } finally {
      setLoadingButton(false);
    };
  };

  if (isLoading) {
    return (<div className="flex flex-col gap-2">
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
    </div>);
  };
  return (
    <React.Fragment>
      <Typography variant="lead" className="font-bold text-black">Supporting Document</Typography>
      <hr className="my-5" />
      <div className="grid grid-cols-12 gap-5">
        {formData.fields.map((item, idx) =>
          <div key={idx} className={item.class}>
            <div className="border-2 border-[#dfe8ff] p-4 rounded-md">
              <div className="flex justify-between items-center">
                <div>
                  <div className="flex items-center gap-1 mb-1">
                    <label htmlFor={item.field_name} className="text-base font-semibold text-black">{item.form_name}</label>
                    {item.is_mandatory ? <span className="text-danger-500">*</span> : null}
                  </div>
                  {JSON.parse(item.notes).map((note, index) => <Typography key={index} variant="small">
                    {JSON.parse(item.notes).length > 1 ? index + 1 + '. ' + note : note}
                  </Typography>)}
                </div>
                {item.value ?
                  <span className="bg-[#f3ffef] text-[#038600] text-sm font-medium me-2 px-2.5 py-0.5 rounded-full">Uploaded</span> :
                  null}
              </div>
              <hr className="my-2" />
              <div className="flex items-center gap-2">
                <div className="border-2 p-2 rounded-md w-full flex items-center gap-2">
                  <img src={item.mimes.includes('jpg') ? jpgIcon : pdfIcon} alt="icon" />
                  {item.value ?
                    <React.Fragment>
                      <Typography variant="small" className="text-[#038600] w-full">The file has been uploaded</Typography>
                      <Button variant="text" className="p-0 hover:bg-transparent hover:text-success-500" onClick={() => previewDocument(idx === 0 || idx === 1 ? 'jpg' : 'pdf', item.value.split(',')[1])}><AiOutlineEye size={20} className="cursor-pointer" /></Button>
                      <span className="text-gray-300">|</span>
                      <Button variant="text" className="p-0 hover:bg-transparent hover:text-danger-500" onClick={() => deleteDocument(item.id)}><RiDeleteBinLine size={20} className="cursor-pointer" /></Button>
                    </React.Fragment> :
                    <input
                      id={item.field_name}
                      name={item.field_name}
                      type={item.form_type}
                      placeholder={item.placeholder}
                      className="block w-full file:py-1 file:px-3 file:rounded-md file:border-0"
                      onChange={(e) => inputFile(e, item.id)}
                      accept={`${idx === 0 ? '.jpg' : idx === 1 ? '.jpg, .jpeg' : '.pdf'}`}
                    />}
                </div>
                <Button
                  loading={loadingButton && (item.id === formId)}
                  type="button"
                  variant="filled"
                  size="sm"
                  className=" bg-primary-500"
                  onClick={() => submitDocument(item.id)}
                  disabled={item.value || (item.id === formId && file ? false : true)}
                >
                  Submit
                </Button>
              </div>
            </div>
          </div>
        )}
      </div>
    </React.Fragment >
  );
};