import * as React from "react";
import { T_ConfirmationModalState } from "../../utils/appForm.type";
import { Button, Dialog, Typography } from "@material-tailwind/react";
import warningImage from "../../../../../public/assets/images/warning.svg";
import garbageImage from "../../../../../public/assets/images/garbage.svg";

type T_FormModal = {
  title?: string;
  subTitle?: string;
  modalState: T_ConfirmationModalState;
  setModalState: React.Dispatch<React.SetStateAction<T_ConfirmationModalState>>
  action: () => void;
  className?: string;
};

const FormModal: React.FC<T_FormModal> = ({title  = "Are you sure want to edit this data?", subTitle = "The data will be changed if you proceed." , modalState, setModalState, action, className}) => {
  return (
  <Dialog open={modalState.visible}>
      <div className={"flex flex-col p-[35px] pt-[50px] gap-y-[20px] text-black " + className}>
        <div className="flex flex-1 justify-center">
          <img
            src={modalState.mode === "Delete" ? garbageImage : warningImage}
            alt={modalState.mode === "Delete" ? "deleteImg" : "warningImg"}
            className="w-[100px] object-center"
          />
        </div>
        <Typography variant="h6" className="text-center font-bold">{modalState.mode === "Delete" ? "Are you sure want to delete this data?" : title}</Typography>
        <Typography variant="paragraph" className="text-sm font-normal text-center text-gray-500">
          {modalState.mode === "Delete" ? "This action cannot be undone." : subTitle}
        </Typography>
        <div className="flex w-full gap-2 justify-center">
          <Button
            color="gray"
            variant="outlined"
            size="lg"
            className="px-20 text-base border-gray-300 normal-case"
            onClick={() => setModalState({ ...modalState, visible: false })}
            disabled={modalState.loading}
          >
            Cancel
          </Button>
          <Button
            type="submit"
            color={modalState.mode === "Delete" ? "red" : "blue"}
            size="lg"
            className={modalState.mode === "Delete" ? "px-20 text-base normal-case bg-red-500" : "px-20 text-base normal-case bg-blue-500"}
            disabled={modalState.loading}
            onClick={action}
            >
              {modalState.mode === "Delete" ? "Yes, Delete" : "Confirm"}
          </Button>
        </div>
      </div>
    </Dialog>
  );
};

export default FormModal;
