import * as React from 'react';
import { Typography, Card, Button } from '@material-tailwind/react';
import { FaPlusCircle } from "react-icons/fa";
import { RiPencilFill, RiDeleteBinFill } from "react-icons/ri";
import { toast } from 'react-toastify';
import services from '../../../../../utils/services';
import { format, parseISO } from 'date-fns';
import { id } from 'date-fns/locale/id';
import { IoDocumentTextOutline } from 'react-icons/io5';
import ConfirmationModal from "../ConfirmationModal";
import { T_ConfirmationModalState } from "../../utils/appForm.type";
import FormRender from '../FormRender';

import '../../assets/css/custom_style.css'

export default function WorkExperience({data}) {
  const [isLoading, setIsLoading] = React.useState(false);
  const [errorMessage, setErrorMessage] = React.useState(null);
  const [isLoadingButton, setIsLoadingButton] = React.useState(false);

  const [formActive, setFormActive] = React.useState(false);
  const [isVisible, setIsVisible] = React.useState<any>(1);
  const [workExperiences, setWorkExperiences] = React.useState([]);
  const [workExperience, setWorkExperience] = React.useState(null);

  const [originalJsonForm, setOriginalJsonForm] = React.useState(null);
  const [formData, setFormData] = React.useState(null);

  const successNotify = (content) => toast.success(content);
  const errorNotify = (content) => toast.error(content);

  const [confirmationModalBox, setConfirmationModalBox] = React.useState<T_ConfirmationModalState>({visible: false, mode: "Edit", loading: false});
  const contractTypeId = "3535E4FAC34B2CBDE0E59A47A73FB2F450ACBD854D8B48A0CBDE3BBB55E2BF2F0D0C77B64F2DCC87A43CF7CBD336B928";

  const fetchData = async () => {
    setIsLoading(true);
    try {
      const responses = await Promise.all([
        services.get('/personal-form/personal-work-experience'),
        services.get(`/master/form-application/${data.id}`),
      ])
      if(responses[0].data.length > 0){
        setIsVisible((responses[0].data[0].is_dont_have === true) ? 1 : 0);
        await Promise.all(responses[0].data.map(async data => {
          if(data.contract_type){
            const getContractType = await services.get(`/master/option/${contractTypeId}`)
            data.contract_name = getContractType.data.find(item => item.value == data.contract_type).label;
          }
          return data;
        }))
      } else {
        setIsVisible(1);
      }
      setWorkExperiences(responses[0].data);
      if(responses[1].data.length > 0){
        customizeFormData(responses[1].data);
      }
      setOriginalJsonForm(responses[1].data);
      setIsLoading(false);
    } catch (error) {
      console.error(error);
      setErrorMessage({title: "Whoops! Something went wrong.", message:"There seems to be a technical issue on our end. Try again later."})
      errorNotify(error.response.status + ' ' + error.response.statusText);
      setIsLoading(false);
      return;
    };
  };

  const handleSubmit = async () => {
    setIsLoadingButton(true);
    const validate = formData.map((field) => {
      if(field.is_mandatory && (field.value === null || field.value === "")){
        field.isError = true;
      } else {
        field.isError = false;
      }
      return field
    })
    setFormData(validate);
    if(validate.filter((field) => field.isError === true).length > 0){
      errorNotify('Please complete the form');
      setIsLoadingButton(false);
      return;
    } else {
      const data:any = {};
      formData.map((field:any) => {
        if(field.field_name != "id"){
          if(formData.find(field => field.field_name === "is_dont_have").value === false){
            if(["start_date", "end_date"].includes(field.field_name)){
              if(field.field_name === "end_date" && formData.find(field => ["is_current", "is_lifetime"].includes(field.field_name)).value === true){
                field.value = format(new Date(), 'yyyy-MM-dd');
              }
              field.value = format(field.value, 'yyyy-MM-dd')
            }
            if(field.field_name === "salary"){
              field.value = (typeof field.value != "number") ? Number(field.value.replace(/[^0-9]/g, "")) : field.value
            }
            if(field.value === ""){
              field.value = null;
            }
          } else {
            if (field.field_name != "is_dont_have") {
              field.value = null
            }
          }
          data[field.field_name] = field.value
          return field;
        }
      })
      try {
        let result;
        const id = formData.find(field => field.field_name === "id").value;
        if(id == "" || id == "0"){
          // create
          result = await services.post(`/personal-form/personal-work-experience`, data)
        } else {
          // edit
          result = await services.put(`/personal-form/personal-work-experience/${id}`, data)
        }
        successNotify(result.message);
        fetchData();
        setIsLoadingButton(false);
        setFormActive(false);
      } catch (error) {
        console.error(error)
        errorNotify(error.response.status + ' ' + error.response.statusText);
        setIsLoadingButton(false);
      }
    }
  }

  const handleDelete = async () => {
    setIsLoading(true);
    setConfirmationModalBox({ ...confirmationModalBox, loading: true });
    try {
      const dataDelete = workExperience;
      const result = await services.delete(`/personal-form/personal-work-experience/${dataDelete.id}`)
      setConfirmationModalBox({ ...confirmationModalBox, visible: false });
      successNotify(result.message);
      setIsLoading(false);
      fetchData();
    } catch (error) {
      console.error(error)
      setConfirmationModalBox({ ...confirmationModalBox, visible: false });
      errorNotify(error.response.status + ' ' + error.response.statusText);
      setIsLoading(false);
    }
  }

  const handleCancel = () => {
    setFormActive(false);
    customizeFormData(originalJsonForm);
    setWorkExperience(null);
  }

  const handleModifyCard = (index, typeModify) => {
    if(typeModify === "edit"){
      setWorkExperience(workExperiences[index]);
      mappingFormwithData(index);
      setFormActive(true);
    } else if(typeModify === "delete") {
      setWorkExperience(workExperiences[index]);
      setConfirmationModalBox({ visible: true, mode: "Delete" });
    }
  }

  const mappingFormwithData = (index?:any) => {
    const newFormData = formData;
    newFormData.map(field => {
      if(field.field_name == "is_dont_have"){
        field.is_visible = isVisible;
      }
      if(index != null){
        if(workExperiences[index]){
          if(workExperiences[index].is_dont_have && !["id", "is_dont_have"].includes(field.field_name)){
            field.value = "";
            if(field.form_type == "checkbox"){
              field.value = false;
            }
          } else {
            field.value = workExperiences[index][field.field_name];
          }
          field.isDisabled = workExperiences[index].is_dont_have;
          field.is_mandatory = (workExperiences[index].is_dont_have) ? false : field.is_mandatory;

          if(field.field_name === "end_date"){
            if(workExperiences[index].is_current === true || workExperiences[index].is_lifetime === true){
              field.form_type = "text";
              field.value = newFormData.find(item => ["is_current", "is_lifetime"].includes(item.field_name)).form_name;
              field.isDisabled = true;
            }
          }
        }
      } else {
        if(field.form_type == "checkbox"){
          field.value = false;
        } else {
          field.value = ""
        }
      }
      return field;
    })
    setFormData(newFormData);
  }

  const customizeFormData = async (data) => {
    const form: any = data;
    form.push({
      field_name: "id",
      value: "0",
      field_type: "hidden"
    })
    const formdata = await Promise.all(form.map(async data => {
      if(data.field_name == "is_dont_have"){
        return {...data, is_visible: isVisible, value: false}
      }
      return {...data, isRequiredLabel: data.is_mandatory, isDisabled: false, isError: false};
    }))
    setFormData(formdata);
  }

  React.useEffect(() => {
    fetchData();
  }, []);

  return (
    <React.Fragment>
      <Card className={`${(workExperiences.length === 0 || !formActive) ? 'flex-1' : ''} w-full overflow-auto shadow-none rounded-none rounded-r-lg border p-6`}>
        <div className="flex justify-between">
          <Typography variant="lead" className="font-bold text-black">Work Experiences</Typography>
          {(!isLoading) && 
            <a 
            onClick={() => {
              let index = null;
              if(workExperiences.length > 0){
                setWorkExperience(workExperiences[0]);
                setIsVisible(workExperiences[0].is_dont_have)
                if (workExperiences[0].is_dont_have == true) {index = 0}
              } else {
                setIsVisible(1)
              }
              mappingFormwithData(index);
              setFormActive(true)
            }} 
            className='cursor-pointer font-bold text-primary-500 flex items-center gap-1'>
            <FaPlusCircle /> Add Work Experience
            </a>
          }
        </div>
        <hr className="my-5" />
        {(isLoading) 
        ? <LoadingDiv/>
        : !errorMessage
          ? 
            <>
              {workExperiences && (workExperiences.length === 0 && !formActive) 
                ? 
                <div className="flex flex-col items-center justify-center my-auto">
                  <Typography>It seems you haven't filled out your latest work experience yet. </Typography>
                  <Typography className="font-bold mt-3">Click Add Work Experience on top right to add more experience</Typography>
                </div> 
                : workExperiences.length > 0 && !formActive
                  ? workExperiences[0].is_dont_have == false
                    ? <ListWorkExperinces datas={workExperiences} actionModify={true} handleModify={handleModifyCard}/>
                    : 
                    <div className="flex flex-col items-center justify-center my-auto">
                      <Typography>You have stated that you don't have work experience.</Typography>
                      <Typography className="font-bold mt-3">Click Add Work Experience on top right to add more experience</Typography>
                    </div> 
                  : null
              }
              {formActive &&
                <FormWorkExperience 
                  formData={formData}
                  setFormData={setFormData}
                  loading={isLoadingButton}
                  handleSubmit={handleSubmit}
                  handleCancel={handleCancel}
                />
              }
            </>
          : 
          <div className="flex flex-col items-center justify-center my-auto">
            <Typography variant="lead" className="font-bold">{errorMessage.title}</Typography>
            <Typography>{errorMessage.message}</Typography>
          </div>
        }
      </Card>
      {(workExperiences.length > 0 && workExperiences[0].is_dont_have == false && formActive) && (
        <Card className="w-full overflow-hidden shadow-none rounded-none rounded-r-lg border p-6">
          <Typography className="font-bold text-black flex items-center gap-3 mb-5"><IoDocumentTextOutline className="w-7 h-7" /> Preview Data</Typography>
          {workExperiences.length > 0
          ? <ListWorkExperinces datas={workExperiences} actionModify={false} handleModify={handleModifyCard}/>
          : null}
        </Card>
      )}
      <ConfirmationModal
        modalState={confirmationModalBox}
        setModalState={setConfirmationModalBox}
        action={confirmationModalBox.mode === "Delete" ? handleDelete : async () => {}}
      />
    </React.Fragment >
  );
};

function LoadingDiv() {
  return (
    <div className="flex flex-col gap-2">
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
    </div>
  )
}

type FormWorkExperienceProps = {
  formData: any;
  setFormData: any;
  loading: any; 
  handleSubmit:any;
  handleCancel:any;
}

function FormWorkExperience(props:FormWorkExperienceProps) {
  const [isLoading, setIsLoading] = React.useState(false);

  React.useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      try {
        await Promise.all(props.formData.map(async (object, key) => {
          if(object.field_type === "select") {
            const newData = props.formData;
            let response;
            switch (object.field_name) {
              case "country_id":
                response = await services.post(`/master/country/list`, {"search": "", "page": 1, "size": 10, "sort": ""});
                response.data = response.data.map(data => {
                  return {label: data.name, value: data.id};
                })
              break;

              case "industry_id":
                response = await services.post(`/master/industry/list`, {"search": "", "page": 1, "size": 10, "sort": ""});
                response.data = response.data.map(data => {
                  return {label: data.name, value: data.id};
                })
              break;

              case "region_id":
                response = await services.post(`/master/region/list`, {"search": "", "page": 1, "size": 10, "sort": ""});
                response.data = response.data.map(data => {
                  return {label: data.province_name, value: data.id};
                })
              break;

              case "contract_type":
                response = await services.get(`/master/option/${object.id}`);
              break;

              default:
                response = {data:[]};
                break;
            }
            newData[key] = {...object, options: response.data}
            props.setFormData(newData);
          }
        }))
        setIsLoading(false);
      } catch (error) {
        console.error(error);
        setIsLoading(false);
        return;
      };
    };

    fetchData();
  }, []);

  return (
    <>
      {(isLoading) && <LoadingDiv/>}
      {(!isLoading) && 
        <>
          {FormRender(props.formData, props.setFormData)}
          <div className="grid grid-col-12 mt-4">
            <div className="col-span-12 flex justify-end items-center gap-2">
              <Button variant="outlined" className="border-primary-500 text-primary-500" onClick={() => props.handleCancel()}>Cancel</Button>
              <Button 
                variant="filled" 
                className="bg-primary-500" 
                loading={props.loading} 
                onClick={() => {
                  props.handleSubmit()
                }}
              >
                {props.loading ? "Loading..." : "Save Form"}
              </Button>
            </div>
          </div>
        </>
      }
    </>
  )
}

function ListWorkExperinces({datas, actionModify, handleModify}:any){
  return <ul className={`${datas.length > 1 ? "progress work-experience" : ""}`}>
    {datas.map((data, id) => {
      return (
        <li key={id}>
          <CardWorkExperience index={id} data={data} actionModify={actionModify} handleModify={handleModify}/>
        </li>
      )
    })}
  </ul>
}

function CardWorkExperience({index, data, actionModify, handleModify}:any){
  const start_date = parseISO(data.start_date.split('T')[0]);
  const end_date = parseISO(data.end_date.split('T')[0]);
  return (
    <div className="flex flex-col gap-3 my-3">
      <div className="flex flex-row items-center gap-2">
        <p className="font-bold text-blue-900">{data.name}</p>
        <p className="font-medium text-slate-400">at {data.company_name}</p>
        <p className="font-medium text-xs bg-purple-50 rounded-lg py-1 px-2">{data.contract_name}</p>
        <p className="font-medium text-xs bg-blue-50 py-1 px-2">{format(start_date, "dd MMM yy", {locale: id})} - {(data.is_current) ? "now" : format(end_date, "dd MMM yy", {locale: id})}</p>
        <RiPencilFill className={`w-4 h-4 cursor-pointer ${(!actionModify) ? 'hidden' : ''}`} onClick={() => handleModify(index, "edit")} />
        <RiDeleteBinFill className={`w-4 h-4 cursor-pointer ${(!actionModify) ? 'hidden' : ''}`} onClick={() => handleModify(index, "delete")} />
      </div>
      <p className="font-medium text-ellipsis overflow-hidden">{data.industry_name} in {data.province_name}, {data.country_name}</p>
      <p>{(data.description) && data.description}</p>
      <div className="bg-slate-50 p-2.5 gap-3">
        <div className="flex gap-2.5"><p>Salary: <span className="font-bold">{(data.salary) ? data.salary + ' IDR': '-'}</span></p><p>Referrer: <span className='font-bold'>{data.recommendation_name} ({data.recommendation_phone_no})</span></p></div>
        <p>{(data.reason_resign) && data.reason_resign}</p>
      </div>
    </div>
  )
}