import * as React from 'react';
import { LiaFileDownloadSolid } from 'react-icons/lia';
import { MdEdit } from 'react-icons/md';
import { Typography, Input, Textarea, Button, Dialog, DialogHeader, DialogBody, DialogFooter } from '@material-tailwind/react';
import userImg from '../../assets/images/user-01.png';
import CloudWhite from '../../../../../public/assets/images/portal/home/CloudWhite.png';

export default function Header() {
  const [openUpdateProfile, setOpenUpdateProfile] = React.useState(false);
  const onOpenUpdateProfile = () => setOpenUpdateProfile((cur) => !cur);
  const [formData, setFormData] = React.useState({ name: '', headline: '', email: '' });

  return (
    <React.Fragment>
      <div
        className="bg-transparent rounded-lg shadow-sm border flex justify-between items-center p-6 bg-cover bg-no-repeat"
        style={{ backgroundImage: `url(${CloudWhite})` }}
      >
        <div className="flex items-center gap-3">
          <img src={userImg} alt="User 1" width={70} className="rounded-full" />
          <div>
            <Typography variant="lead" className="font-bold text-black flex items-center gap-2">
              Alexander Christie
              <MdEdit size={18} className="cursor-pointer" onClick={onOpenUpdateProfile} />
            </Typography>
            <Typography variant="small">Alexander Christie</Typography>
          </div>
        </div>
        <div>
          <Button variant="filled" className="bg-white text-primary-500 border-dashed border border-primary-500 rounded-full flex items-center gap-1">
            <LiaFileDownloadSolid size={24} />
            Download CV
          </Button>
        </div>
      </div>
      <Dialog className="p-4" size="sm" open={openUpdateProfile} handler={onOpenUpdateProfile}>
        <DialogHeader className="flex flex-col items-start gap-1 pb-0">
          <Typography variant="lead" className="font-bold text-black">Your Profile</Typography>
          <Typography variant="small">Last edit on 19 June 2024</Typography>
        </DialogHeader>
        <DialogBody className="flex flex-col items-center gap-3">
          <div className="w-full">
            <img src={userImg} alt="User 1" width={70} className="rounded-full" />
          </div>
          <div className="w-full">
            <label htmlFor="title">Name</label>
            <Input
              labelProps={{ className: "hidden" }}
              size="lg"
              id="name"
              type="text"
              placeholder="Input job title"
              className={`!border-2 !border-gray-300 placeholder:text-gray-400 placeholder:opacity-100 focus:!outline-none focus:!border-primary-500 mt-1`}
              onChange={({ target }) => setFormData(prevData => ({ ...prevData, name: target.value }))}
              value={formData.name}
            />
          </div>
          <div className="w-full">
            <label htmlFor="title">Headline</label>
            <Textarea
              labelProps={{ className: "hidden" }}
              id="headline"
              placeholder="Input job title"
              className={`!border-2 !border-gray-300 placeholder:text-gray-400 placeholder:opacity-100 focus:!outline-none focus:!border-primary-500 mt-1`}
              onChange={({ target }) => setFormData(prevData => ({ ...prevData, headline: target.value }))}
              value={formData.headline}
            />
          </div>
          <div className="w-full -mt-2">
            <label htmlFor="title">Email</label>
            <Input
              labelProps={{ className: "hidden" }}
              size="lg"
              id="email"
              type="text"
              placeholder="Input job title"
              className={`!border-2 !border-gray-300 placeholder:text-gray-400 placeholder:opacity-100 focus:!outline-none focus:!border-primary-500 mt-1`}
              onChange={({ target }) => setFormData(prevData => ({ ...prevData, email: target.value }))}
              value={formData.email}
            />
          </div>
        </DialogBody>
        <DialogFooter className="flex justify-end gap-2">
          <Button variant="outlined" className="border-gray-400 text-gray-600" onClick={onOpenUpdateProfile}>Cancel</Button>
          <Button
            variant="filled"
            className="bg-primary-500"
            onClick={onOpenUpdateProfile}
          >
            Save Changes
          </Button>
        </DialogFooter>
      </Dialog>
    </React.Fragment>
  );
};