import * as React from 'react';
import { useState, useEffect } from 'react';
import { Typography, Button, IconButton, Checkbox, Input, Option, Popover, PopoverHandler, PopoverContent } from '@material-tailwind/react';
import { format, set } from "date-fns";
import { DayPicker } from "react-day-picker";
import Select from 'react-select';
import { IoIosAddCircle } from "react-icons/io";
import { IoTrash } from "react-icons/io5";
import { RiPencilFill } from "react-icons/ri";
import { IoDocumentTextOutline, IoChevronBack, IoChevronForward } from "react-icons/io5";
import { MdOutlineDateRange } from "react-icons/md";
import services from '../../../../../utils/services';
import { toast } from 'react-toastify';

const dummyData = [
  {
    name: 'Winner Of Masterchef Indonesia',
    organization: 'Direktorat Pembinaan',
    level: 'Nasional',
    category: 'Tim',
    year: '2023'
  },
]

type T_Option = {
  label: string,
  value: string,
}

type T_Field = {
  id: string | null,
  form_name: string | null,
  field_type: 'input' | 'select',
  field_name: string | null,
  form_type: 'text' | 'number' | 'date',
  max_value: number | null,
  is_visible: boolean,
  is_mandatory: boolean,
  tab_form_application_id: string,
  class: string | null,
  sort: number,
  is_other: boolean | null,
  notes: string | null,
  tooltip: string | null,
  placeholder: string | null,
  tab_name: string | null,
  is_privacy: boolean | null,
  max_upload: number | null,
  mimes: string | null,
  options: T_Option[] | null,
}

export default function Achievement() {
  const [formID, setFormID] = useState<string>('');
  const [formOpen, setFormOpen] = useState({open: false, status: 'add'});
  const [formDisabled, setFormDisabled] = useState<boolean>(false);
  const [achievementData, setAchievementData] = useState([]);
  const [selectedData, setSelectedData] = useState<string>('');
  const [fields, setFields] = useState<T_Field[]>([]);
  const [renderFields, setRenderFields] = useState<T_Field[]>([]);
  const [inputs, setInputs] = useState({});
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    getPersonalAchievementData();
    getFormId();
  }, [])

  useEffect(() => {
    fields.forEach((item) => {
      if(item.field_type === 'select' && item.form_type !== 'date') {
        getFieldOptions(item.id);
      }
    });
  }, [fields])
  
  // useEffect(() => {
  //   console.log(inputs);
  // }, [inputs])

  const getFormId = async () => {
    try {
      setLoading(true);
      const response = await services.get('/master/tab-form');

      if(response.statusCode === 200) {
        const form = response.data.find((item) => item.name === 'Achievement');
        setFormID(form.id);
        getFormFields(form.id);
        setLoading(false);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to get form ID data');
    }
  }

  const getFormFields = async (id: string) => {
    try {
      setLoading(true);
      const response = await services.get(`/master/form-application/${id}`);
      
      if(response.statusCode === 200) {
        setFields(response.data);
        setRenderFields(response.data);
        setLoading(false);
        // console.log(response.data);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to get form fields data');
    }
  }

  const getFieldOptions = async (id: string) => {
    try {
      setLoading(true);
      const response = await services.get(`/master/option/${id}`);

      if(response.statusCode === 200) {
        // console.log(response.data);
        setRenderFields((prev) => prev.map(item => item.id === id ? ({...item, options: response.data}) : item));
        setLoading(false);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to get form field options data');
    }
  }

  const getPersonalAchievementData = async () => {
    try {
      setLoading(true);
      const response = await services.get('/personal-form/personal-achievement/list');

      if(response.statusCode === 200) {
        setAchievementData(response.dataAchievement);
        setLoading(false);
        // console.log(response.dataAchievement);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to get personal Achievement data');
    }
  }

  const onSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    try {
      setLoading(true);
      if(formOpen.status === 'add') {
        const response = await services.post('/personal-form/personal-achievement/save', {
          tab_id: formID,
          ...inputs,
          is_dont_have: formDisabled,
        });

        if(response.statusCode === 201) {
          setInputs({});
          toast.success('New personal Achievement data added successfully');
          setFormOpen(prev => ({...prev, open: false}));
          getPersonalAchievementData();
        }
      }
      
      if(formOpen.status === 'edit') {
        const response = await services.post(`/personal-form/personal-achievement/update/${selectedData}`, {
          ...inputs,
        });

        if(response.statusCode === 200) {
          setInputs({});
          setSelectedData('');
          toast.success('Personal Achievement data edited successfully');
          setFormOpen(prev => ({...prev, open: false}));
          getPersonalAchievementData();
        }
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error(`Failed to ${formOpen.status === 'add' ? 'add new' : 'edit'} personal Achievement data`);
    }
  }

  const onEdit = (item) => {
    setInputs(item);
    setSelectedData(item.id);
    setFormOpen({open: true, status: 'edit'});
  }

  const onDelete = async (id) => {
    try {
      setLoading(true);
      const response = await services.delete(`/personal-form/personal-achievement/delete/${id}`);

      if(response.statusCode === 200) {
        toast.success('Personal Achievement data deleted successfully');
        getPersonalAchievementData();
        setLoading(false);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to delete personal Achievement data');
    }
  }

  const onRenderField = (item: T_Field) => {
    switch(item.field_type) {
      case 'input':
        return (
          <div key={item.id} className={`flex flex-col flex-1 gap-y-2 ${item.class}`}>
            <Typography className="text-black font-normal">
              <>
                {item.form_name} {item.is_mandatory ? <span className='text-red-500'>*</span> : ''}
              </>
            </Typography>
            <Input
              size="lg"
              color="blue"
              type={item.form_type}
              placeholder={item.placeholder}
              required={item.is_mandatory}
              className="!border !border-gray-300 bg-white text-gray-900 ring-4 ring-transparent placeholder:text-gray-500 placeholder:opacity-100 focus:!border-primary-500 focus:!border-t-primary-500"
              labelProps={{
                className: "hidden",
              }}
              value={inputs[item.field_name] || ''}
              onChange={(e) => setInputs((prev) => ({...prev, [item.field_name]: e.target.value}))}
              disabled={formDisabled || loading}
            />
          </div>
        )
      case 'select':
        return (
          <div key={item.id} className={`flex flex-col flex-1 gap-y-2 ${item.class}`}>
            <Typography className="text-black font-normal">
              <>
                {item.form_name} {item.is_mandatory ? <span className='text-red-500'>*</span> : ''}
              </>
            </Typography>
            {item.form_type !== 'date'
            ? <Select
                placeholder={item.placeholder}
                required={item.is_mandatory}
                isDisabled={formDisabled || loading}
                components={{
                  IndicatorSeparator: () => null
                }}
                options={item.options}
                defaultValue={formOpen.status === 'edit' && {value: inputs[item.field_name] || '', label: item.options.find((item1) => item1.value === inputs[item.field_name])?.label || ''}}
                onChange={(e) => setInputs((prev) => ({...prev, [item.field_name]: e.value}))}
              />
            : <div className="w-full">
                <Popover placement="bottom">
                  <PopoverHandler>
                    <Input
                      icon={<MdOutlineDateRange size={20} className='text-gray-500'/>}
                      labelProps={{ className: "hidden" }}
                      size="lg"
                      id="from_date"
                      type="text"
                      placeholder="From date"
                      className="!border !border-gray-300 bg-white text-gray-900 ring-4 ring-transparent placeholder:text-gray-500 placeholder:opacity-100 focus:!border-primary-500 focus:!border-t-primary-500"
                      containerProps={{ className: "min-w-[8px] w-full" }}
                      required={item.is_mandatory}
                      value={inputs[item.field_name] || ''}
                      onChange={(e) => setInputs((prev) => ({...prev, [item.field_name]: e.target.value}))}
                      disabled={formDisabled || loading}
                    />
                  </PopoverHandler>
                  <PopoverContent>
                    <DayPicker
                      mode="single"
                      selected={inputs[item.field_name] || ''}
                      onSelect={(date) => setInputs((prev) => ({...prev, [item.field_name]: parseInt(format(date, 'yyyy'))}))}
                      // showOutsideDays
                      className="border-0"
                      classNames={{
                        caption: "flex justify-center py-2 mb-4 relative items-center",
                        caption_label: "text-sm font-medium text-gray-900",
                        nav: "flex items-center",
                        nav_button:
                          "h-6 w-6 bg-transparent hover:bg-blue-gray-50 p-1 rounded-md transition-colors duration-300",
                        nav_button_previous: "absolute left-1.5",
                        nav_button_next: "absolute right-1.5",
                        table: "w-full border-collapse",
                        head_row: "flex font-medium text-gray-900",
                        head_cell: "m-0.5 w-9 font-normal text-sm",
                        row: "flex w-full mt-2",
                        cell: "text-gray-600 rounded-md h-9 w-9 text-center text-sm p-0 m-0.5 relative [&:has([aria-selected].day-range-end)]:rounded-r-md [&:has([aria-selected].day-outside)]:bg-gray-900/20 [&:has([aria-selected].day-outside)]:text-white [&:has([aria-selected])]:bg-gray-900/50 first:[&:has([aria-selected])]:rounded-l-md last:[&:has([aria-selected])]:rounded-r-md focus-within:relative focus-within:z-20",
                        day: "h-9 w-9 p-0 font-normal",
                        day_range_end: "day-range-end",
                        day_selected:
                          "rounded-md bg-gray-900 text-white hover:bg-gray-900 hover:text-white focus:bg-gray-900 focus:text-white",
                        day_today: "rounded-md bg-gray-200 text-gray-900",
                        day_outside:
                          "day-outside text-gray-500 opacity-50 aria-selected:bg-gray-500 aria-selected:text-gray-900 aria-selected:bg-opacity-10",
                        day_disabled: "text-gray-500 opacity-50",
                        day_hidden: "invisible",
                      }}
                      components={{
                        IconLeft: ({ ...props }) => (
                          <IoChevronBack {...props} className="h-4 w-4 stroke-2" />
                        ),
                        IconRight: ({ ...props }) => (
                          <IoChevronForward {...props} className="h-4 w-4 stroke-2" />
                        ),
                      }}
                    />
                  </PopoverContent>
                </Popover>
              </div>
            }
          </div>
        )
      default:
        return (<></>)
    }
  }

  return (
    <>
      <div className='flex flex-col h-full p-6 gap-y-5 bg-white rounded-r-lg'>
        <div className='flex gap-4 items-center justify-between'>
          <Typography className="font-bold text-black">Achievement</Typography>
          {!formOpen.open && <Button
            variant="text"
            color="blue"
            className="flex p-0 gap-1 text-primary-500 normal-case items-center hover:bg-transparent hover:text-primary-300"
            onClick={() => setFormOpen({open: true, status: 'add'})}
            disabled={loading}
            loading={loading}
          >
            <IoIosAddCircle size={20}/>
            Add Achievement
          </Button>}
        </div>
        <hr/>

        {formOpen.open && <div className='flex flex-col gap-y-5'>
          {!achievementData.length && <Checkbox
            color="blue"
            label="I Don't have any achievement"
            checked={formDisabled}
            onChange={() => setFormDisabled(prev => !prev)}
          />}

          <form className='grid grid-cols-12 gap-6'>
            {renderFields.map((item) => onRenderField(item))}

            <div className='flex gap-2 items-center justify-end col-span-12'>
              <Button
                variant="outlined"
                color="blue"
                className="border-primary-500 text-primary-500 normal-case"
                onClick={() => {
                  setFormOpen((prev) => ({...prev, open: false}))
                  setInputs({});
                }}
                disabled={loading}
              >
                Cancel
              </Button>
              <Button
                variant="filled"
                color="blue"
                className="bg-primary-500 normal-case shadow-none hover:shadow-none hover:bg-primary-400"
                type="submit"
                onClick={onSubmit}
                disabled={loading}
                loading={loading}
              >
                {formOpen.status === "add" ? "Add Achievement" : "Edit Achievement"}
              </Button>
            </div>
          </form>
        </div>}

        <div className='flex flex-col h-full gap-y-4'>
          {!formOpen.open && (loading
            ? <>
                <div className='flex flex-col gap-y-1'>
                  <Typography className="h-[26px] w-[250px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                  <Typography className="h-[26px] w-[250px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                </div>
                <div className='flex flex-col gap-y-1'>
                  <Typography className="h-[26px] w-[250px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                  <Typography className="h-[26px] w-[250px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                </div>
                <div className='flex flex-col gap-y-1'>
                  <Typography className="h-[26px] w-[250px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                  <Typography className="h-[26px] w-[250px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                </div>
                <div className='flex flex-col gap-y-1'>
                  <Typography className="h-[26px] w-[250px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                  <Typography className="h-[26px] w-[250px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                </div>
              </>
            : achievementData.length
              ? achievementData.map((item, i) => (
                <div key={i} className='flex flex-col gap-y-1'>
                  <div className='flex gap-1 items-center'>
                    <Typography className="text-primary-900 font-bold">{item.name}</Typography>
                    <span className='px-2 py-1 text-black bg-orange-50'>{item.year}</span>
                    <IconButton
                      variant="text"
                      className="p-0 w-5 h-5 hover:bg-transparent"
                      onClick={() => onEdit(item)}
                      disabled={loading}
                    >
                      <RiPencilFill size={20} className='text-blue-gray-800 hover:text-blue-gray-600'/>
                    </IconButton>
                    <IconButton
                      variant="text"
                      className="p-0 w-5 h-5 hover:bg-transparent"
                      onClick={() => onDelete(item.id)}
                      disabled={loading}
                    >
                      <IoTrash size={20} className='text-blue-gray-800 hover:text-blue-gray-600'/>
                    </IconButton>
                  </div>
                  <Typography className="text-black font-normal text-sm">{item.organization_name}, Tingkat {item.level}, Kategori {item.category}</Typography>
                </div>
              ))
              : <div className='flex flex-col w-full h-full gap-4 items-center justify-center'>
                  <Typography className="text-black font-normal text-center">It seems you haven't filled out your achievement yet.</Typography>
                  <Typography variant="h5" className="text-black font-bold text-center">Click Add Achievement on top right to add your achievement</Typography>
                </div>
          )}
        </div>
      </div>

      {formOpen.open && achievementData.length > 0 && <div className='flex flex-col h-full p-6 mt-4 gap-y-5 bg-white rounded-r-lg overflow-y-auto'>
        <Typography className="flex gap-x-2 font-bold text-black items-center"><IoDocumentTextOutline size={30}/> Preview Data</Typography>
        {achievementData.map((item, i) => (
          <div key={i} className='flex flex-col gap-y-1'>
            <div className='flex gap-1 items-center'>
              <Typography className="text-primary-900 font-bold">{item.name}</Typography>
              <span className='px-2 py-1 text-black bg-orange-50'>{item.year}</span>
            </div>
            <Typography className="text-black font-normal text-sm">{item.organization_name}, Tingkat {item.level}, Kategori {item.category}</Typography>
          </div>
        ))}
      </div>}
    </>
  )
}