import * as React from 'react';
import { toast } from 'react-toastify';
import { Card, List, ListItem, ListItemPrefix, ListItemSuffix, Typography } from '@material-tailwind/react';
import { useNavigate, useParams } from 'react-router-dom';
import BasicInformation from './Content/BasicInformation';
import Address from './Content/Address';
import Education from './Content/Education';
import WorkExperience from './Content/WorkExperience';
import OrganizationalExperience from './Content/OrganizationalExperience';
import TrainingCertification from './Content/TrainingCertification';
import Skill from './Content/Skill';
import Achievement from './Content/Achievement';
import SocialMedia from './Content/SocialMedia';
import Family from './Content/Family';
import FamilyBI from './Content/FamilyBI';
import SupportingDocument from './Content/SupportingDocument';
import UserIcon from '../assets/icons/User';
import MapIcon from '../assets/icons/Map';
import MortarboardIcon from '../assets/icons/Mortarboard';
import BriefcaseIcon from '../assets/icons/Briefcase';
import TeamworkIcon from '../assets/icons/Teamwork';
import AnalysisIcon from '../assets/icons/Analysis';
import SkillIcon from '../assets/icons/Skill';
import AwardIcon from '../assets/icons/Award';
import SocialMediaIcon from '../assets/icons/SocialMedia';
import FamilyIcon from '../assets/icons/Family';
import GoogleDocsIcon from '../assets/icons/GoogleDocs';
import services from '../../../../utils/services';
import checklistIcon from '../assets/icons/checklist.png'

type Tab = {
  id: string;
  name: string;
  icon: string;
  sort: number;
  is_multiple: number;
  is_mandatory: number;
  code_tab: string;
};

const iconsMap: { [key: string]: React.FC } = {
  'UserIcon': UserIcon,
  'MapIcon': MapIcon,
  'MortarboardIcon': MortarboardIcon,
  'BriefcaseIcon': BriefcaseIcon,
  'TeamworkIcon': TeamworkIcon,
  'AnalysisIcon': AnalysisIcon,
  'SkillIcon': SkillIcon,
  'AwardIcon': AwardIcon,
  'SocialMediaIcon': SocialMediaIcon,
  'FamilyIcon': FamilyIcon,
  'GoogleDocsIcon': GoogleDocsIcon,
};

const TabsMap: { [key: string]: React.FC<{ data: Tab }> } = {
  'basic_information': BasicInformation,
  'address_domicile': Address,
  'education': Education,
  'work_experience': WorkExperience,
  'organizational_experience': OrganizationalExperience,
  'training_certification': TrainingCertification,
  'skill': Skill,
  'achievement': Achievement,
  'social_media': SocialMedia,
  'family': Family,
  'family_members': FamilyBI,
  'supporting_document': SupportingDocument
};

export default function FormContent() {
  const { code } = useParams<{ code: string }>();
  const navigate = useNavigate();
  const errorNotify = (content) => toast.error(content);
  const [tabs, setTabs] = React.useState([]);
  const [data, setData] = React.useState<Tab | undefined>(undefined);

  const fetchData = async () => {
    try {
      const result = await services.get('/master/tab-form');
      setTabs(result.data);
      if (!code) navigate('/application-form/' + result.data[0].code_tab);
    } catch (error) {
      errorNotify(error.response.statusText);
    };
  };

  React.useEffect(() => {
    fetchData();
  }, []);

  React.useEffect(() => {
    const currentTab = tabs.find(tab => tab.code_tab === location.pathname.split('/')[2]);
    setData(currentTab);
  }, [location.pathname, tabs.length]);

  const forms = React.useCallback(() => {
    const codeTab = location.pathname.split('/')[2];
    const Component = TabsMap[codeTab];
    return Component && data ? <Component data={data} /> : null;
  }, [data, location.pathname]);

  const icons = (icon: string) => {
    const IconComponent = iconsMap[icon];
    return IconComponent ? <IconComponent /> : null;
  };

  if (!tabs.length) {
    return (<div className="flex flex-col gap-2">
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
    </div>);
  };

  return (
    <div className="flex mt-2">
      <div className="h-screen w-full max-w-[20rem] p-4 border rounded-l-lg">
        <List>
          {tabs.map((item, idx) =>
            <React.Fragment key={idx}>
              <ListItem onClick={() => navigate('/application-form/' + item.code_tab)} selected={location.pathname === `/application-form/${item.code_tab}`}
                style={{ backgroundColor: location.pathname === `/application-form/${item.code_tab}` ? '#EFF2FF' : 'transparent' }}>
                <ListItemPrefix>
                  {icons(item.icon)}
                </ListItemPrefix>
                {item.name}
                <ListItemSuffix>
                  <img src={checklistIcon} alt="Checklist" />
                </ListItemSuffix>
              </ListItem>
              {idx !== tabs.length - 1 ?
                <hr /> :
                null}
            </React.Fragment>
          )}
        </List>
      </div>
      {!['education', 'work_experience', 'organizational_experience', 'training_certification', 'social_media'].includes(location.pathname.split('/')[2]) ? (
        ['achievement', 'family', 'family_members'].includes(location.pathname.split('/')[2]) ? (
          <Card className="w-full overflow-hidden shadow-none rounded-none rounded-r-lg border-none bg-transparent">
            <>{forms()}</>
          </Card>
        ) : (
          <Card className="w-full overflow-hidden shadow-none rounded-none rounded-r-lg border p-6">
            <>{forms()}</>
          </Card>
        )
      ) : (
        <div className="w-full overflow-hidden flex flex-col gap-5">
          {forms()}
        </div>
      )}
    </div>
  );
};