import * as React from 'react';
import { Typography, Button, Tooltip, Input, Textarea, Checkbox } from '@material-tailwind/react';
import { toast } from 'react-toastify';
import { LuFileEdit } from "react-icons/lu";
import { AiOutlineInfoCircle, AiOutlineEye, AiOutlineEyeInvisible } from 'react-icons/ai';
import { useLocation } from 'react-router-dom';
import ReactSelect from 'react-select';
import services from '../../../../../utils/services';

type Field = {
  id: string;
  form_name: string;
  field_type: string;
  field_name: string;
  form_type: string;
  max_value: number;
  is_privacy: number;
  is_visible: number;
  is_mandatory: number;
  tab_form_application_id: string;
  class: string;
  sort: number;
  is_other: any;
  notes: string;
  tooltip: string;
  placeholder: string;
  value: any;
  valueView: string;
};

export default function Address({ data }) {
  const location = useLocation();
  const [isUpdateForm, setIsUpdateForm] = React.useState(false);
  const successNotify = (content) => toast.success(content);
  const errorNotify = (content) => toast.error(content);
  const [isLoading, setIsLoading] = React.useState(false);
  const [loadingButton, setLoadingButton] = React.useState(false);
  const [isSameDomicile, setIsSameDomicile] = React.useState(false);
  const [formData, setFormData] = React.useState({
    isSubmit: false,
    fields: [
      {
        id: '',
        form_name: '',
        field_type: '',
        field_name: '',
        form_type: '',
        max_value: 0,
        is_privacy: 0,
        is_visible: 0,
        is_mandatory: 0,
        tab_form_application_id: '',
        class: '',
        sort: 0,
        is_other: null,
        notes: null,
        tooltip: null,
        placeholder: '',
        tab_name: '',
        value: null,
        valueView: ''
      }
    ]
  });
  const [options, setOptions] = React.useState({
    provincesAddress: [], districtsAddress: [], subDistrictsAddress: [],
    provincesCard: [], districtsCard: [], subDistrictsCard: []
  });

  const dangerStyles = {
    control: (provided, state) => ({
      ...provided,
      // border: formData.isSubmit && !formData['1'].title ? '2px solid #ef4444' : state.isFocused ? '2px solid #1566D1' : '2px solid #d1d5db',
      borderRadius: '0.375rem', // Adjust border radius as needed
      height: '44px',
      minHeight: '44px',
    }),
    placeholder: (provided) => ({
      ...provided,
      color: '#9ca3af', // Adjust color as needed
    }),
  };

  React.useEffect(() => {
    if (location.pathname.includes(data.code_tab)) isUpdateForm ? fetchData() : fetchForm();
  }, [data, isUpdateForm]);

  React.useEffect(() => {
    if (isSameDomicile) {
      formData.fields.forEach(async (item) => {
        if (item.field_name === 'province_address') { await fetchDistrictCard(item.value) };
        if (item.field_name === 'city_address') { await fetchSubDistrictCard(item.value) };
      });

      setFormData(prevData => {
        const updatedFields = prevData.fields.map(field => {
          switch (field.field_name) {
            case 'domicile_card':
              return { ...field, value: prevData.fields.find(f => f.field_name === 'domicile_address').value };
            case 'province_card':
              return { ...field, value: prevData.fields.find(f => f.field_name === 'province_address').value };
            case 'city_card':
              return { ...field, value: prevData.fields.find(f => f.field_name === 'city_address').value };
            case 'sub_district_card':
              return { ...field, value: prevData.fields.find(f => f.field_name === 'sub_district_address').value };
            case 'zip_code_card':
              return { ...field, value: prevData.fields.find(f => f.field_name === 'zip_code_address').value };
            default:
              return field;
          };
        });
        return { ...prevData, fields: updatedFields };
      });
    };
  }, [isSameDomicile]);

  const fetchDistrictAddress = async (provinceCode) => {
    const districts = await services.get('/master/region/district/' + provinceCode);
    setOptions((prevData) => ({ ...prevData, districtsAddress: districts.data }));
  };

  const fetchSubDistrictAddress = async (districtCode) => {
    const subDistricts = await services.get('/master/region/subdistrict/' + districtCode);
    setOptions((prevData) => ({ ...prevData, subDistrictsAddress: subDistricts.data }));
  };

  const fetchDistrictCard = async (provinceCode) => {
    const districts = await services.get('/master/region/district/' + provinceCode);
    setOptions((prevData) => ({ ...prevData, districtsCard: districts.data }));
  };

  const fetchSubDistrictCard = async (districtCode) => {
    const subDistricts = await services.get('/master/region/subdistrict/' + districtCode);
    setOptions((prevData) => ({ ...prevData, subDistrictsCard: subDistricts.data }));
  };

  const fetchForm = async () => {
    setIsLoading(true);
    try {
      const forms = await services.get('/master/form-application/' + data.id);
      setFormData((prevData) => ({ ...prevData, fields: forms.data }));
    } catch (error) {
      errorNotify(error.response.statusText);
    } finally {
      setIsLoading(false);
    };
  };

  const fetchData = async () => {
    setIsLoading(true);
    try {
      const forms = await services.get('/master/form-application/' + data.id);
      setFormData((prevData) => ({ ...prevData, fields: forms.data }));
      // get district (chaining dari province), get subdistrict (chaining dari district) 
      forms.data.forEach(async (item) => {
        if (item.field_name === 'province_address') { await fetchDistrictAddress(item.value) };
        if (item.field_name === 'city_address') { await fetchSubDistrictAddress(item.value) };
        if (item.field_name === 'province_card') { await fetchDistrictCard(item.value) };
        if (item.field_name === 'city_card') { await fetchSubDistrictCard(item.value) };
      });

      const provinces = await services.get('/master/region/province');
      setOptions((prevData) => ({ ...prevData, provincesAddress: provinces.data }));
      setOptions((prevData) => ({ ...prevData, provincesCard: provinces.data }));
    } catch (error) {
      errorNotify(error.response.statusText);
    } finally {
      setIsLoading(false);
    };
  };

  // Toggle is_privacy value function
  const togglePrivacy = (indices) => {
    setFormData(prevFormData => ({
      ...prevFormData,
      fields: prevFormData.fields.map((field, idx) =>
        indices.includes(idx) ? { ...field, is_privacy: field.is_privacy === 0 ? 1 : 0 } : field
      )
    }));
  };

  const extractData = (fields: Field[]) => {
    return fields.reduce((acc, field) => {
      acc[field.field_name] = field.value;
      return acc;
    }, {} as Record<string, string>);
  };

  const sameDomicile = (fieldName, e) => {
    if (fieldName === 'province_address') { fetchDistrictAddress(e.province_code) };
    if (fieldName === 'city_address') { fetchSubDistrictAddress(e.district_code) };
    if (fieldName === 'province_card') { fetchDistrictCard(e.province_code) };
    if (fieldName === 'city_card') { fetchSubDistrictCard(e.district_code) };
  };

  const switchOption = (fieldName, value = '') => {
    switch (fieldName) {
      case 'province_address':
        return { value: value ? options.provincesAddress.find(option => option.province_code === value) : null, options: options.provincesAddress };
      case 'city_address':
        return { value: value ? options.districtsAddress.find(option => option.district_code === value) : null, options: options.districtsAddress };
      case 'sub_district_address':
        return { value: value ? options.subDistrictsAddress.find(option => option.sub_district_code === value) : null, options: options.subDistrictsAddress };
      case 'province_card':
        return { value: value ? options.provincesCard.find(option => option.province_code === value) : null, options: options.provincesCard };
      case 'city_card':
        return { value: value ? options.districtsCard.find(option => option.district_code === value) : null, options: options.districtsCard };
      case 'sub_district_card':
        return { value: value ? options.subDistrictsCard.find(option => option.sub_district_code === value) : null, options: options.subDistrictsCard };
      default:
        break;
    };
  };

  const forms = (item: Field, idx: number) => {
    switch (item.field_type) {
      case 'input':
        const input = item.form_type === 'checkbox' ?
          <div className={item.class}>
            <Checkbox
              label={<Typography variant="small" className="ml-2 text-gray-500"><>{item.form_name}</></Typography>}
              className="w-4 h-4 rounded checked:border-primary-500 checked:bg-primary-500"
              containerProps={{ className: "p-0" }}
              checked={item.value}
              onChange={() => {
                setIsSameDomicile((prevData) => !prevData);
                setFormData(prevData => {
                  const updatedFields = prevData.fields.map((item) => {
                    if (item.field_name === 'is_same') return { ...item, value: item.value ? 0 : 1 };
                    return item;
                  });
                  return { ...prevData, fields: updatedFields };
                });
              }}
            />
          </div> :
          <div className={item.class}>
            <label htmlFor={item.field_name} className='flex items-center gap-1'>
              {item.form_name}
              {item.tooltip ? <Tooltip content={item.tooltip}>
                <Button variant="text" size="lg" className="p-0"><AiOutlineInfoCircle className="text-gray-500" /></Button>
              </Tooltip> : null}
              {item.is_mandatory ? <span className="text-danger-500">*</span> : null}
            </label>
            <Input
              labelProps={{ className: "hidden" }}
              size="lg"
              id={item.field_name}
              name={item.field_name}
              type={item.field_type}
              placeholder={item.placeholder}
              className={`${formData.isSubmit && !item.value ? '!border-danger-500' : '!border-gray-300'} placeholder:text-gray-400 placeholder:opacity-100 focus:!outline-none focus:!border-primary-500 mt-1`}
              onChange={({ target }) => {
                if (isSameDomicile && item.field_name === 'zip_code_address') {
                  setFormData(prevData => {
                    const updatedFields = prevData.fields.map((item) => {
                      if (item.field_name === 'is_same') return { ...item, value: item.value ? 0 : 1 };
                      setIsSameDomicile((prevData) => !prevData);
                      return item;
                    });
                    return { ...prevData, fields: updatedFields };
                  });
                };
                setFormData(prevData => ({ ...prevData, fields: prevData.fields.map((item, index) => index === idx ? { ...item, value: target.value } : item) }))
              }}
              value={item.value}
              disabled={item.field_name === 'zip_code_card' && isSameDomicile}
            />
          </div>
        return input;
      case 'select':
        return (<div className={item.class}>
          <label htmlFor={item.field_name} className='flex items-center gap-1'>
            {item.form_name}
            {item.tooltip ? <Tooltip content={item.tooltip}>
              <Button variant="text" size="lg" className="p-0"><AiOutlineInfoCircle className="text-gray-500" /></Button>
            </Tooltip> : null}
            {item.is_mandatory ? <span className="text-danger-500">*</span> : null}
          </label>
          <ReactSelect
            components={{ IndicatorSeparator: () => null }}
            id={item.field_name}
            placeholder={item.placeholder}
            className="mt-1"
            styles={dangerStyles}
            options={switchOption(item.field_name).options}
            onChange={(e) => {
              if (isSameDomicile) {
                setFormData(prevData => {
                  const updatedFields = prevData.fields.map((item) => {
                    if (item.field_name === 'is_same') return { ...item, value: item.value ? 0 : 1 };
                    setIsSameDomicile((prevData) => !prevData);
                    return item;
                  });
                  return { ...prevData, fields: updatedFields };
                });
              };
              sameDomicile(item.field_name, e);
              setFormData(prevData =>
              ({
                ...prevData, fields: prevData.fields.map((item, index) => index === idx ?
                  {
                    ...item, value: item.field_name === 'province_address' || item.field_name === 'province_card' ? e.province_code :
                      item.field_name === 'city_address' || item.field_name === 'city_card' ? e.district_code :
                        item.field_name === 'sub_district_address' || item.field_name === 'sub_district_card' ? e.sub_district_code : e.value
                  } : item)
              }));
            }}
            value={switchOption(item.field_name, item.value).value}
            isDisabled={(item.field_name === 'province_card' || item.field_name === 'city_card' || item.field_name === 'sub_district_card') && isSameDomicile}
          />
          {/* {(formData.isSubmit && !item.value) && <span className="text-danger-500 mt-1">Vacancy stream is required</span>} */}
        </div>);
      case 'textarea':
        return (<div className={item.class}>
          <label htmlFor={item.field_name} className='flex items-center gap-1'>
            {item.form_name}
            {item.tooltip ? <Tooltip content={item.tooltip}>
              <Button variant="text" size="lg" className="p-0"><AiOutlineInfoCircle className="text-gray-500" /></Button>
            </Tooltip> : null}
            {item.is_mandatory ? <span className="text-danger-500">*</span> : null}
          </label>
          <Textarea
            labelProps={{ className: "hidden" }}
            id={item.field_name}
            name={item.field_name}
            placeholder={item.placeholder}
            className={`${formData.isSubmit && !item.value ? '!border-danger-500' : '!border-gray-300'} placeholder:text-gray-400 placeholder:opacity-100 focus:!outline-none focus:!border-primary-500 mt-1`}
            onChange={({ target }) => {
              if (isSameDomicile) {
                setFormData(prevData => {
                  const updatedFields = prevData.fields.map((item) => {
                    if (item.field_name === 'is_same') return { ...item, value: item.value ? 0 : 1 };
                    setIsSameDomicile((prevData) => !prevData);
                    return item;
                  });
                  return { ...prevData, fields: updatedFields };
                });
              };
              setFormData(prevData => ({ ...prevData, fields: prevData.fields.map((item, index) => index === idx ? { ...item, value: target.value } : item) }))
            }}
            value={item.value}
            disabled={item.field_name === 'domicile_card' && isSameDomicile}
          />
          {/* {(formData.isSubmit && !item.value) && <span className="text-danger-500 mt-1">Vacancy stream is required</span>} */}
        </div>);
      default:
        break;
    };
  };

  const saveFormData = async (event) => {
    event.preventDefault();
    setLoadingButton(true);
    setFormData(prevData => ({ ...prevData, isSubmit: true }));
    const resultData = extractData(formData.fields);
    try {
      const response = await services.post('/personal-form/personal-address', { ...resultData, tab_id: data.id });
      setIsUpdateForm((prevData) => !prevData);
      successNotify(response.message);
    } catch (error) {
      errorNotify(error.response.statusText);
    } finally {
      setLoadingButton(false);
    };
  };

  if (isLoading) {
    return (<div className="flex flex-col gap-2">
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
    </div>);
  };

  return (
    <React.Fragment>
      <div className="flex justify-between items-center">
        <Typography variant="lead" className="font-bold text-black">Address</Typography>
        {!isUpdateForm ? <div className="flex items-center gap-1">
          {formData.fields[0].is_privacy ?
            <AiOutlineEyeInvisible size={20} className="cursor-pointer" onClick={() => togglePrivacy(Array.from({ length: 12 }, (_, i) => i))} /> :
            <AiOutlineEye size={20} className="cursor-pointer" onClick={() => togglePrivacy(Array.from({ length: 12 }, (_, i) => i))} />} |
          <LuFileEdit size={15} className="cursor-pointer" onClick={() => setIsUpdateForm((prevData) => !prevData)} />
        </div> : null}
      </div>
      <hr className="my-5" />
      {!isUpdateForm ? <div className="grid grid-cols-12 gap-5">
        {formData.fields.map((item, idx) =>
          <div key={idx} className={item.class}>
            {item.field_name !== 'is_same' ?
              <React.Fragment>
                <Typography variant="small" className="font-bold text-[#002759] mb-1.5">{item.form_name}</Typography>
                <Typography variant="small">{item.is_privacy ? '********' : item.valueView}</Typography>
              </React.Fragment> :
              null}
          </div>
        )}
      </div> :
        <form onSubmit={saveFormData}>
          <div className="grid grid-cols-12 gap-5">
            {formData.fields.map((item, idx) =>
              <React.Fragment key={idx}>
                {forms(item, idx)}
              </React.Fragment>
            )}
          </div>
          <div className="flex justify-end items-center gap-2 mt-5">
            <Button variant="outlined" className="border-primary-500 text-primary-500" onClick={() => setIsUpdateForm((prevData) => !prevData)}>Cancel</Button>
            <Button loading={loadingButton} type="submit" variant="filled" className="bg-primary-500">Save Form</Button>
          </div>
        </form>}
    </React.Fragment>
  );
};