import * as React from 'react';
import { FaPlusCircle } from 'react-icons/fa';
import { MdEdit } from 'react-icons/md';
import { IoDocumentTextOutline } from 'react-icons/io5';
import { RiDeleteBinFill, RiDeleteBinLine } from 'react-icons/ri';
import { IoChevronBack, IoChevronForward } from 'react-icons/io5';
import { AiOutlineInfoCircle, AiOutlineEye } from 'react-icons/ai';
import { Typography, Input, Textarea, Popover, PopoverHandler, PopoverContent, Button, Tooltip, Card } from '@material-tailwind/react';
import { format } from 'date-fns';
import { DayPicker } from 'react-day-picker';
import { toast } from 'react-toastify';
import { useLocation } from 'react-router-dom';
import ReactSelect from 'react-select';
import services from '../../../../../utils/services';
import pdfIcon from '../../assets/images/pdf-icon.png';

import '../../assets/css/custom_style.css';

interface Field {
  id: string;
  form_name: string;
  field_type: string;
  field_name: string;
  form_type: string;
  max_value: number;
  is_border_top: number;
  is_mandatory: number;
  tab_form_application_id: string;
  class: string;
  sort: number;
  is_other: number;
  notes: string | null;
  tooltip: string | null;
  placeholder: string;
  tab_name: string;
  is_privacy: number;
  max_upload: number | null;
  mimes: string | null;
  is_comma: string | null;
  value: string | null;
}

export default function Education({ data }) {
  const location = useLocation();
  const successNotify = (content) => toast.success(content);
  const errorNotify = (content) => toast.error(content);
  const [isLoading, setIsLoading] = React.useState(false);
  const [loadingButton, setLoadingButton] = React.useState(false);
  const [action, setAction] = React.useState('list');
  const [formId, setFormId] = React.useState('');
  const [sort, setSort] = React.useState(0);
  const [checkOther, setCheckOther] = React.useState({ label: '', visibility: false });
  const [formData, setFormData] = React.useState({
    isSubmit: false,
    lists: [],
    fields: [
      {
        "id": "1C90F85441BF5E6D68770417EC34895759D85B9D5A76C2919EB6745B020FA74D7FB617090AAF9B9D8F00ED1B7BFC243D",
        "form_name": "Education Level",
        "field_type": "select",
        "field_name": "education_level_id",
        "form_type": "text",
        "max_value": 50,
        "is_border_top": 0,
        "is_mandatory": 1,
        "tab_form_application_id": "E7C4E7567393FCCDE3C96FF9837A6E21E3C4CF07BC8AEEE21731A965DECC2E968706E633D46BB2531C96867212FB49B2",
        "class": "col-span-12 md:col-span-4",
        "sort": 1,
        "is_other": 0,
        "notes": null,
        "tooltip": null,
        "placeholder": "Choose your last education level",
        "tab_name": "education",
        "is_privacy": 0,
        "max_upload": null,
        "mimes": null,
        "is_comma": null,
        "value": null
      }
    ]
  });
  const [options, setOptions] = React.useState({
    educationLevels: [],
    locations: [],
    universities: [],
    cities: [],
    universityTypes: [],
    faculties: [],
    fieldStudies: [],
    majors: [],
    titles: [],
    gap1: [],
    gap2: [],
    gap3: [],
    startEducation: [],
    graduationDate: [],
    scholarships: [],
    ijazahs: [],
    transkrips: []
  });

  const dangerStyles = {
    control: (provided, state) => ({
      ...provided,
      // border: formData.isSubmit && !formData['1'].title ? '2px solid #ef4444' : state.isFocused ? '2px solid #1566D1' : '2px solid #d1d5db',
      borderRadius: '0.375rem', // Adjust border radius as needed
      height: '44px',
      minHeight: '44px',
    }),
    placeholder: (provided) => ({
      ...provided,
      color: '#9ca3af', // Adjust color as needed
    }),
  };

  React.useEffect(() => {
    if (checkOther.label === 'Lainnya') {
      setCheckOther((prevData) => ({ ...prevData, visibility: true }));
    } else {
      setCheckOther((prevData) => ({ ...prevData, visibility: false }));
    };
  }, [checkOther.label]);

  React.useEffect(() => {
    if (location.pathname.includes(data.code_tab)) action !== 'list' ? fetchData() : fetchForm();
  }, [data, action]);

  const fetchUniversity = async (value) => {
    const universities = await services.get(`/master/education?searchTerm=${value}&limit=100&offset=0`);
    setOptions((prevData) => ({ ...prevData, universities: universities.data }));
  };

  const fetchMajor = async (fieldOfStudyCode) => {
    const majors = await services.get('/master/major/from/' + fieldOfStudyCode);
    setOptions((prevData) => ({ ...prevData, majors: majors.data }));
  };

  const fetchForm = async () => {
    setIsLoading(true);
    try {
      const forms = await services.get(`/personal-form/personal-education/${action === 'list' || action === 'add' ? action : 'get'}${action === 'edit' ? '/' + formId : ''}`);
      if (action === 'list') { setFormData((prevData) => ({ ...prevData, lists: forms.data })) };
      setFormData((prevData) => ({ ...prevData, fields: forms.data }));
    } catch (error) {
      errorNotify(error.response.statusText);
    } finally {
      setIsLoading(false);
    };
  };

  const fetchData = async () => {
    setIsLoading(true);
    try {
      const forms = await services.get(`/personal-form/personal-education/${action === 'list' || action === 'add' ? action : 'get'}${action === 'edit' ? '/' + formId : ''}`);
      if (action === 'list') { setFormData((prevData) => ({ ...prevData, lists: forms.data })) };
      setFormData((prevData) => ({ ...prevData, fields: forms.data }));
      // get data location, education_type, scholarship_type, certificate_type, transcript_type
      forms.data.forEach(async (item) => {
        switch (item.field_name) {
          case 'location':
            const locations = await services.get('/master/option/' + item.id);
            setOptions((prevData) => ({ ...prevData, locations: locations.data }));
            break;
          case 'education_type':
            const universityTypes = await services.get('/master/option/' + item.id);
            setOptions((prevData) => ({ ...prevData, universityTypes: universityTypes.data }));
            break;
          case 'field_study_id':
            fetchMajor(item.value);
            break;
          case 'scholarship_type':
            const scholarships = await services.get('/master/option/' + item.id);
            setOptions((prevData) => ({ ...prevData, scholarships: scholarships.data }));
            break;
          case 'certificate_type':
            const ijazahs = await services.get('/master/option/' + item.id);
            setOptions((prevData) => ({ ...prevData, ijazahs: ijazahs.data }));
            break;
          case 'transcript_type':
            const transkrips = await services.get('/master/option/' + item.id);
            setOptions((prevData) => ({ ...prevData, transkrips: transkrips.data }));
            break;
          default:
            break;
        };
      });

      const educationLevels = await services.get('/master/education_level');
      setOptions((prevData) => ({ ...prevData, educationLevels: educationLevels.data }));

      const universities = await services.get('/master/education');
      setOptions((prevData) => ({ ...prevData, universities: universities.data }));

      const cities = await services.get('/master/region-district');
      setOptions((prevData) => ({ ...prevData, cities: cities.data }));

      const faculties = await services.get('/master/faculty');
      setOptions((prevData) => ({ ...prevData, faculties: faculties.data }));

      const fieldStudies = await services.get('/master/field_study');
      setOptions((prevData) => ({ ...prevData, fieldStudies: fieldStudies.data }));

      const titles = await services.get('/master/degree');
      setOptions((prevData) => ({ ...prevData, titles: titles.data }));

      const gap1 = Array.from({ length: 4 }, (_, index) => ({ label: String(index + 1), value: String(index + 1) }));
      const gap2 = Array.from({ length: 10 }, (_, index) => ({ label: String(index), value: String(index) }));

      const startEducation = Array.from({ length: 24 }, (_, index) => ({ label: String(index + 2000), value: String(index + 2000) }));
      const graduationDate = Array.from({ length: 50 }, (_, index) => ({ label: String(index + 2000), value: String(index + 2000) }));
      setOptions((prevData) => ({ ...prevData, gap1: gap1, gap2: gap2, gap3: gap2, startEducation: startEducation, graduationDate: graduationDate }));
    } catch (error) {
      errorNotify(error.response.statusText);
    } finally {
      setIsLoading(false);
    };
  };

  const inputFile = async (e: React.ChangeEvent<HTMLInputElement>, fieldName: string) => {
    setFormId(formId);
    if (e.target.files && e.target.files.length > 0) {
      const selectedFile = e.target.files[0];
      setFormData(prevData => ({ ...prevData, fields: prevData.fields.map((item) => item.field_name === fieldName ? { ...item, value: selectedFile } : item) }));
    } else {
      setFormData(prevData => ({ ...prevData, fields: prevData.fields.map((item) => item.field_name === fieldName ? { ...item, value: null } : item) }));
    };
  };

  const switchOption = (fieldName, value = '') => {
    switch (fieldName) {
      case 'education_level_id':
        return { value: value ? options.educationLevels.find(option => option.value === value) : null, options: options.educationLevels };
      case 'location':
        return { value: value ? options.locations.find(option => option.value === value) : null, options: options.locations };
      case 'education_id':
        return { value: value ? options.universities.find(option => option.value === value) : null, options: options.universities };
      case 'district_code':
        return { value: value ? options.cities.find(option => option.value === value) : null, options: options.cities };
      case 'education_type':
        return { value: value ? options.universityTypes.find(option => option.value === value) : null, options: options.universityTypes };
      case 'faculty_id':
        return { value: value ? options.faculties.find(option => option.value === value) : null, options: options.faculties };
      case 'field_study_id':
        return { value: value ? options.fieldStudies.find(option => option.value === value) : null, options: options.fieldStudies };
      case 'major_id':
        return { value: value ? options.majors.find(option => option.value === value) : null, options: options.majors };
      case 'degree_id':
        return { value: value ? options.titles.find(option => option.value === value) : null, options: options.titles };
      case 'gpa_1':
        return { value: value ? options.gap1.find(option => option.value === value) : null, options: options.gap1 };
      case 'gpa_2':
        return { value: value ? options.gap2.find(option => option.value === value) : null, options: options.gap2 };
      case 'gpa_3':
        return { value: value ? options.gap3.find(option => option.value === value) : null, options: options.gap3 };
      case 'start_date':
        return { value: value ? options.startEducation.find(option => option.value === value) : null, options: options.startEducation };
      case 'end_date':
        return { value: value ? options.graduationDate.find(option => option.value === value) : null, options: options.graduationDate };
      case 'scholarship_type':
        return { value: value ? options.scholarships.find(option => option.value === value) : null, options: options.scholarships };
      case 'certificate_type':
        return { value: value ? options.ijazahs.find(option => option.value === value) : null, options: options.ijazahs };
      case 'transcript_type':
        return { value: value ? options.transkrips.find(option => option.value === value) : null, options: options.transkrips };
      default:
        break;
    };
  };

  const forms = (item, idx: number) => {
    switch (item.field_type) {
      case 'input':
        const input = item.form_type === 'date' ?
          <div className={item.class}>
            <label htmlFor={item.field_name} className='flex items-center gap-1'>
              {item.form_name}
              {item.tooltip ? <Tooltip content={item.tooltip}>
                <Button variant="text" size="lg" className="p-0"><AiOutlineInfoCircle className="text-gray-500" /></Button>
              </Tooltip> : null}
              {item.is_mandatory ? <span className="text-danger-500">*</span> : null}
            </label>
            <Popover placement="left-start">
              <PopoverHandler>
                <Input
                  labelProps={{ className: "hidden" }}
                  size="lg"
                  id={item.field_name}
                  type="text"
                  placeholder={item.placeholder}
                  className="!border-gray-300 placeholder:text-gray-400 placeholder:opacity-100 focus:!outline-none focus:!border-primary-500"
                  containerProps={{ className: "min-w-[8px] mt-1" }}
                  onChange={() => null}
                  value={item.value ? format(item.value, 'dd-MM-yyyy') : ''}
                />
              </PopoverHandler>
              <PopoverContent>
                <DayPicker
                  mode="single"
                  selected={new Date(item.value)}
                  onSelect={(date) => setFormData(prevData => ({ ...prevData, fields: prevData.fields.map((item, index) => index === idx ? { ...item, value: String(date) } : item) }))}
                  showOutsideDays
                  className="border-0"
                  classNames={{
                    caption: "flex justify-center py-2 mb-4 relative items-center",
                    caption_label: "text-sm font-medium text-gray-900",
                    nav: "flex items-center",
                    nav_button:
                      "h-6 w-6 bg-transparent hover:bg-blue-gray-50 p-1 rounded-md transition-colors duration-300",
                    nav_button_previous: "absolute left-1.5",
                    nav_button_next: "absolute right-1.5",
                    table: "w-full border-collapse",
                    head_row: "flex font-medium text-gray-900",
                    head_cell: "m-0.5 w-9 font-normal text-sm",
                    row: "flex w-full mt-2",
                    cell: "text-gray-600 rounded-md h-9 w-9 text-center text-sm p-0 m-0.5 relative [&:has([aria-selected].day-range-end)]:rounded-r-md [&:has([aria-selected].day-outside)]:bg-gray-900/20 [&:has([aria-selected].day-outside)]:text-white [&:has([aria-selected])]:bg-gray-900/50 first:[&:has([aria-selected])]:rounded-l-md last:[&:has([aria-selected])]:rounded-r-md focus-within:relative focus-within:z-20",
                    day: "h-9 w-9 p-0 font-normal",
                    day_range_end: "day-range-end",
                    day_selected:
                      "rounded-md bg-gray-900 text-white hover:bg-gray-900 hover:text-white focus:bg-gray-900 focus:text-white",
                    day_today: "rounded-md bg-gray-200 text-gray-900",
                    day_outside:
                      "day-outside text-gray-500 opacity-50 aria-selected:bg-gray-500 aria-selected:text-gray-900 aria-selected:bg-opacity-10",
                    day_disabled: "text-gray-500 opacity-50",
                    day_hidden: "invisible",
                  }}
                  components={{
                    IconLeft: ({ ...props }) => (
                      <IoChevronBack {...props} className="h-4 w-4 stroke-2" />
                    ),
                    IconRight: ({ ...props }) => (
                      <IoChevronForward {...props} className="h-4 w-4 stroke-2" />
                    ),
                  }}
                />
              </PopoverContent>
            </Popover>
          </div> :
          item.form_type === 'file' ?
            <React.Fragment>
              {item.field_name === 'file_transcript' || item.field_name === 'file_certificate' ?
                <div className="col-span-12">
                  <Typography variant="lead" className="font-bold text-black mb-3">Data Pendukung</Typography>
                  <hr />
                </div> :
                null}
              <div className={item.class}>
                <div className="border-2 border-[#dfe8ff] p-4 rounded-md">
                  <div className="flex justify-between items-center">
                    <div>
                      <div className="flex items-center gap-1 mb-1">
                        <label htmlFor={item.field_name} className="text-base font-semibold text-black">{item.form_name}</label>
                        {item.is_mandatory ? <span className="text-danger-500">*</span> : null}
                      </div>
                      {JSON.parse(item.notes).map((note, index) => <Typography key={index} variant="small">
                        {JSON.parse(item.notes).length > 1 ? index + 1 + '. ' + note : note}
                      </Typography>)}
                    </div>
                    {item.value && typeof item.value === 'string' ?
                      <span className="bg-[#f3ffef] text-[#038600] text-sm font-medium me-2 px-2.5 py-0.5 rounded-full">Uploaded</span> :
                      null}
                  </div>
                  <hr className="my-2" />
                  <div className="flex items-center gap-2">
                    <div className="border-2 p-2 rounded-md w-full flex items-center gap-2">
                      <img src={pdfIcon} alt="icon" />
                      {item.value ?
                        <React.Fragment>
                          <Typography variant="small" className="text-[#038600] w-full">
                            {item.value && typeof item.value === 'string' ? 'The file has been uploaded' : 'The file is ready to be uploaded'}
                          </Typography>
                          <Button
                            variant="text"
                            className="p-0 hover:bg-transparent hover:text-green-500"
                            onClick={() => previewDocument(item.value)}
                          >
                            <AiOutlineEye size={20} className="cursor-pointer" />
                          </Button>
                          <span className="text-gray-300">|</span>
                          <Button
                            variant="text"
                            className="p-0 hover:bg-transparent hover:text-danger-500"
                            onClick={() => deleteDocument(item)}
                          >
                            <RiDeleteBinLine size={20} className="cursor-pointer" />
                          </Button>
                        </React.Fragment> :
                        <input
                          id={item.field_name}
                          name={item.field_name}
                          type={item.form_type}
                          placeholder={item.placeholder}
                          className="block w-full file:py-1 file:px-3 file:rounded-md file:border-0"
                          onChange={(e) => inputFile(e, item.field_name)}
                          accept={`${idx === 0 ? '.jpg' : idx === 1 ? '.jpg, .jpeg' : '.pdf'}`}
                        />}
                    </div>
                  </div>
                </div>
              </div>
            </React.Fragment> :
            <div
              className={`${item.class} ${item.sort === 10 ||
                item.sort === 11 || item.sort === 22 ?
                item.sort === 10 &&
                  formData.fields[6].value &&
                  formData.fields[6].value === '4459174713D1B750ADA2825CFE81C1D6DC0CACA5F95539D2557C852913B4AF14E0C107740ECCACBF3FBC48C9DA43F553' ?
                  'visible' :
                  item.sort === 11 &&
                    checkOther.visibility ?
                    'visible' :
                    item.sort === 22 &&
                      formData.fields[18].value &&
                      formData.fields[18].value !== 'none' ?
                      'visible' :
                      'invisible' :
                'visible'}`}>
              <label htmlFor={item.field_name} className='flex items-center gap-1'>
                {item.form_name}
                {item.tooltip ? <Tooltip content={item.tooltip}>
                  <Button variant="text" size="lg" className="p-0"><AiOutlineInfoCircle className="text-gray-500" /></Button>
                </Tooltip> : null}
                {item.is_mandatory ? <span className="text-danger-500">*</span> : null}
              </label>
              <Input
                labelProps={{ className: "hidden" }}
                size="lg"
                id={item.field_name}
                name={item.field_name}
                type={item.field_type}
                placeholder={item.placeholder}
                className={`${formData.isSubmit && !item.value ? '!border-danger-500' : '!border-gray-300'} placeholder:text-gray-400 placeholder:opacity-100 focus:!outline-none focus:!border-primary-500 mt-1`}
                onChange={({ target }) => setFormData(prevData => ({ ...prevData, fields: prevData.fields.map((item, index) => index === idx ? { ...item, value: target.value } : item) }))}
                value={item.value}
              />
            </div>
        return input;
      case 'select':
        return (<div className={`${item.class} ${item.field_name === 'gpa_1' ? 'relative' : ''}`}>
          <label htmlFor={item.field_name} className='flex items-center gap-1'>
            {item.form_name}
            {item.tooltip ? <Tooltip content={item.tooltip}>
              <Button variant="text" size="lg" className="p-0"><AiOutlineInfoCircle className="text-gray-500" /></Button>
            </Tooltip> : null}
            {item.is_mandatory ? <span className="text-danger-500">*</span> : null}
          </label>
          <ReactSelect
            components={{ IndicatorSeparator: () => null }}
            id={item.field_name}
            placeholder={item.placeholder}
            className="mt-1"
            styles={dangerStyles}
            options={switchOption(item.field_name).options}
            onInputChange={(e) => { if (item.field_name === 'education_id') { fetchUniversity(e) } }}
            onChange={(e) => {
              if (item.field_name === 'major_id') { setCheckOther((prevData) => ({ ...prevData, label: e.label })) };
              if (item.field_name === 'field_study_id') { fetchMajor(e.value) };
              setFormData(prevData =>
              ({
                ...prevData, fields: prevData.fields.map((item, index) => index === idx ?
                  { ...item, value: e.value } : item)
              }));
            }}
            value={switchOption(item.field_name, item.value).value}
          />
          {item.is_comma ? <span className="mt-12 absolute top-0 -right-3">,</span> : null}
          {/* {(formData.isSubmit && !item.value) && <span className="text-danger-500 mt-1">Vacancy stream is required</span>} */}
        </div>);
      case 'textarea':
        return (<div className={item.class}>
          <label htmlFor={item.field_name} className='flex items-center gap-1'>
            {item.form_name}
            {item.tooltip ? <Tooltip content={item.tooltip}>
              <Button variant="text" size="lg" className="p-0"><AiOutlineInfoCircle className="text-gray-500" /></Button>
            </Tooltip> : null}
            {item.is_mandatory ? <span className="text-danger-500">*</span> : null}
          </label>
          <Textarea
            labelProps={{ className: "hidden" }}
            id={item.field_name}
            name={item.field_name}
            placeholder={item.placeholder}
            className={`${formData.isSubmit && !item.value ? '!border-danger-500' : '!border-gray-300'} placeholder: text - gray - 400 placeholder: opacity - 100 focus: !outline - none focus: !border - primary - 500 mt - 1`}
            onChange={({ target }) => setFormData(prevData => ({ ...prevData, fields: prevData.fields.map((item, index) => index === idx ? { ...item, value: target.value } : item) }))}
            value={item.value}
          />
          {/* {(formData.isSubmit && !item.value) && <span className="text-danger-500 mt-1">Vacancy stream is required</span>} */}
        </div>);
      default:
        break;
    };
  };

  const previewDocument = (file) => {
    if (typeof file === 'object') {
      const fileUrl = URL.createObjectURL(file);
      const newTab = window.open();
      if (newTab) {
        newTab.document.body.innerHTML = `
          <object data="${fileUrl}" type="application/pdf" width="100%" height="100%">
            <p>PDF viewer tidak didukung di browser ini. Anda bisa <a href="${fileUrl}">unduh PDF</a> untuk melihatnya.</p>
          </object>
        `;
      }
      // Revoke the object URL after it's used to free up resources
      newTab.onunload = () => URL.revokeObjectURL(fileUrl);
    } else {
      const newTab = window.open();
      if (newTab) {
        newTab.document.body.innerHTML = `
          <object data="data:application/pdf;base64,${file.split(',')[1]}" type="application/pdf" width="100%" height="100%">
            <p>PDF viewer tidak didukung di browser ini. Anda bisa <a href="data:application/pdf;base64,${file.split(',')[1]}">unduh PDF</a> untuk melihatnya.</p>
          </object>
        `;
      };
    };
  };

  const deleteDocument = async (data) => {
    setFormData(prevData => ({ ...prevData, fields: prevData.fields.map((item) => item.field_name === data.field_name ? { ...item, value: null } : item) }));
  };
  // const deleteDocument = async (data) => {
  //   setLoadingButton(true);
  //   if (data.value && data.value === 'string') {
  //     try {
  //       const response = await services.delete('/personal-form/personal-documents/' + data.id);
  //       fetchData();
  //       successNotify(response.message);
  //     } catch (error) {
  //       errorNotify(error.response.statusText);
  //     } finally {
  //       setLoadingButton(false);
  //     };
  //   } else {
  //     setFormData(prevData => ({ ...prevData, fields: prevData.fields.map((item) => item.field_name === data.field_name ? { ...item, value: null } : item) }));
  //     setLoadingButton(false);
  //   };
  // };

  const saveFormData = async (event) => {
    event.preventDefault();
    setLoadingButton(true);
    setFormData(prevData => ({ ...prevData, isSubmit: true }));
    const resultData = formData.fields.reduce((acc, field) => {
      if (field.value) {
        acc[field.field_name] = field.value;
      }
      return acc;
    }, {} as Record<string, string>);

    const formDataObj = new FormData();
    Object.entries({ ...resultData, tab_id: data.id, sort: action === 'edit' ? sort : formData.lists.length + 1 }).forEach(([key, value]) => {
      formDataObj.append(key, value);
    });

    try {
      const response = await services.post(`/personal-form/personal-education${action === 'edit' ? `/${formId}` : ''}`, formDataObj,
        { headers: { 'Content-Type': 'multipart/form-data' } });
      setAction('list');
      successNotify(response.message);
    } catch (error) {
      errorNotify(error.response.statusText);
    } finally {
      setLoadingButton(false);
    };
  };

  const deleteFormData = async (formId: string) => {
    setLoadingButton(true);
    try {
      const response = await services.delete('/personal-form/personal-education/' + formId);
      fetchData();
      successNotify(response.message);
    } catch (error) {
      errorNotify(error.response.statusText);
    } finally {
      setLoadingButton(false);
    };
  };

  function ListEducation({ item }: any) {
    return (<li className="mb-4">
      <div className="flex items-center gap-2">
        <Typography variant="small" className="font-bold text-md text-[#002759]">{item.education_level_name}, {item.education_name}, {item.city_name}</Typography>
        <span className="bg-[#fff4d3] tex-gray-800 text-xs font-semibold me-2 px-2.5 py-0.5 rounded">{item.end_date}</span>
        {action === 'list' ?
          <React.Fragment>
            <MdEdit size={18} className="cursor-pointer hover:text-success-500" onClick={() => { setAction('edit'); setFormId(item.id); setSort(item.sort); }} />
            <RiDeleteBinFill size={18} className="cursor-pointer hover:text-danger-500" onClick={() => deleteFormData(item.id)} />
          </React.Fragment> :
          null}
      </div>
      <div className="flex items-center gap-1 my-2">
        <span className="bg-[#f0f0f0] text-black text-xs font-semibold me-2 px-2.5 py-0.5 rounded">GPA {item.gpa} with title {item.degree_name}</span>
        <span className="bg-[#f0f0f0] text-black text-xs font-semibold me-2 px-2.5 py-0.5 rounded">NIM. {item.student_no}</span>
        <span className="bg-[#f0f0f0] text-black text-xs font-semibold me-2 px-2.5 py-0.5 rounded">No. Ijazah {item.certificate_number}</span>
      </div>
      <div className="flex flex-col gap-2 text-black">
        <div className="flex items-center gap-2">
          <div className="bg-black w-1 h-1 rounded-full"></div><Typography variant="small"><span className="font-semibold">Education Date: </span>{item.start_date} - {item.end_date}</Typography>
        </div>
        <div className="flex items-center gap-2">
          <div className="bg-black w-1 h-1 rounded-full"></div><Typography variant="small"><span className="font-semibold">Type University: </span>{item.education_type?.toUpperCase()}</Typography>
        </div>
        <div className="flex items-center gap-2">
          <div className="bg-black w-1 h-1 rounded-full"></div><Typography variant="small"><span className="font-semibold">Faculty of </span>{item.faculty_name}, <span className="font-semibold">Major </span>{item.major_name}, <span className="font-semibold">Field of Study </span>{item.field_study_name}</Typography>
        </div>
        <div className="flex items-center gap-2">
          <div className="bg-black w-1 h-1 rounded-full"></div><Typography variant="small"><span className="font-semibold">Full Scholarship: </span>{item.scholarship_name}</Typography>
        </div>
      </div>
    </li>)
  };

  if (isLoading) {
    return (<Card className="min-h-screen shadow-none rounded-none rounded-r-lg border p-6 flex flex-col gap-2">
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
    </Card>);
  };
  return (
    <React.Fragment>
      <Card className="min-h-screen shadow-none rounded-none rounded-r-lg border p-6">
        <div className="flex justify-between items-center">
          <Typography variant="lead" className="font-bold text-black">Education</Typography>
          {action === 'list' ?
            <Button variant="text" className="flex items-center gap-2 text-primary-500 hover:bg-primary-50" onClick={() => setAction('add')} >
              <FaPlusCircle size={20} className="cursor-pointer" />
              <Typography variant="small" className="font-bold">Add Education</Typography>
            </Button> :
            null}
        </div>
        <hr className="my-5" />
        {action === 'list' ?
          <ul className="progress work-experience">
            {formData.lists.map((item, idx) =>
              <ListEducation key={idx} item={item} />)}
          </ul> :
          <form onSubmit={saveFormData}>
            <div className="grid grid-cols-12 gap-5">
              {formData.fields.map((item, idx) =>
                <React.Fragment key={idx}>
                  {forms(item, idx)}
                </React.Fragment>
              )}
            </div>
            <div className="flex justify-end items-center gap-2 mt-5">
              <Button variant="outlined" className="border-primary-500 text-primary-500" onClick={() => setAction('list')}>Cancel</Button>
              <Button loading={loadingButton} type="submit" variant="filled" className="bg-primary-500">Save Form</Button>
            </div>
          </form>}
      </Card>
      {action === 'edit' ?
        <Card className="shadow-none rounded-none rounded-r-lg border p-6">
          <div className="flex items-center gap-2 mb-3">
            <IoDocumentTextOutline size={20} />
            <Typography variant="lead" className="font-bold text-black">Preview Data</Typography>
          </div>
          <ul className="progress work-experience">
            {formData.lists.map((item, idx) =>
              <ListEducation key={idx} item={item} />)}
          </ul>
        </Card> :
        null}
    </React.Fragment>
  );
};