import * as React from 'react';
import { Typography, Card, Button } from '@material-tailwind/react';
import { toast } from 'react-toastify';
import ConfirmationModal from "../ConfirmationModal";
import { T_ConfirmationModalState } from "../../utils/appForm.type";
import services from '../../../../../utils/services';
import { FaPlusCircle } from "react-icons/fa";
import { IoDocumentTextOutline } from 'react-icons/io5';
import FormRender from '../FormRender';
import { RiPencilFill, RiDeleteBinFill } from "react-icons/ri";

import Facebook from '../../assets/icons/SocialMedia/Facebook';
import Instagram from '../../assets/icons/SocialMedia/Instagram';
import Linkedin from '../../assets/icons/SocialMedia/Linkedin';
import Telegram from '../../assets/icons/SocialMedia/Telegram';
import TikTok from '../../assets/icons/SocialMedia/TikTok';
import Twitter from '../../assets/icons/SocialMedia/Twitter';

export default function SocialMedia({data}) {
  const [isLoading, setIsLoading] = React.useState(false);
  const [errorMessage, setErrorMessage] = React.useState(null);
  const [isLoadingButton, setIsLoadingButton] = React.useState(false);

  const [formActive, setFormActive] = React.useState(false);
  const [isVisible, setIsVisible] = React.useState<any>(1);
  const [socialMedias, setSocialMedias] = React.useState([]);
  const [socialMedia, setSocialMedia] = React.useState(null);

  const [originalJsonForm, setOriginalJsonForm] = React.useState(null);
  const [formData, setFormData] = React.useState(null);

  const successNotify = (content) => toast.success(content);
  const errorNotify = (content) => toast.error(content);

  const [confirmationModalBox, setConfirmationModalBox] = React.useState<T_ConfirmationModalState>({visible: false, mode: "Edit", loading: false});


  const fetchData = async () => {
    setIsLoading(true);
    try {
      const responses = await Promise.all([
        services.get('/personal-form/personal-social-media'),
        services.get(`/master/form-application/${data.id}`),
      ])
      if(responses[0].data.length > 0){
        setIsVisible((responses[0].data[0].is_dont_have === true) ? 1 : 0);
        await Promise.all(responses[0].data.map(async data => {
          if(data.social_media_id){
            const getSocialMedia = await services.get(`/master/social-media/${data.social_media_id}`)
            data.icon = getSocialMedia.data.icon;
          }
          return data;
        }))
      } else {
        setIsVisible(1);
      }
      setSocialMedias(responses[0].data);
      if(responses[1].data.length > 0){
        customizeFormData(responses[1].data);
      }
      setOriginalJsonForm(responses[1].data);
      setIsLoading(false);
    } catch (error) {
      console.error(error);
      setErrorMessage({title: "Whoops! Something went wrong.", message:"There seems to be a technical issue on our end. Try again later."})
      errorNotify(error.response.status + ' ' + error.response.statusText);
      setIsLoading(false);
      return;
    };
  };

  const handleSubmit = async () => {
    setIsLoadingButton(true);
    const validate = formData.map((field) => {
      if(field.is_mandatory && (field.value === null || field.value === "")){
        field.isError = true;
      } else {
        field.isError = false;
      }
      return field
    })
    setFormData(validate);
    if(validate.filter((field) => field.isError === true).length > 0){
      errorNotify('Please complete the form');
      setIsLoadingButton(false);
      return;
    } else {
      const data:any = {};
      formData.map((field:any) => {
        if(field.field_name != "id"){
          if(formData.find(field => field.field_name === "is_dont_have").value === false){
            if(field.value === ""){
              field.value = null;
            }
          } else {
            if (field.field_name != "is_dont_have") {
              field.value = null
            }
          }
          data[field.field_name] = field.value
          return field;
        }
      })
      try {
        let result;
        const id = formData.find(field => field.field_name === "id").value;
        if(id == "" || id == "0"){
          // create
          result = await services.post(`/personal-form/personal-social-media`, data)
        } else {
          // edit
          result = await services.put(`/personal-form/personal-social-media/${id}`, data)
        }
        successNotify(result.message);
        fetchData();
        setIsLoadingButton(false);
        setFormActive(false);
      } catch (error) {
        console.error(error)
        errorNotify(error.response.status + ' ' + error.response.statusText);
        setIsLoadingButton(false);
      }
    }
  }

  const handleDelete = async () => {
    setIsLoading(true);
    setConfirmationModalBox({ ...confirmationModalBox, loading: true });
    try {
      const dataDelete = socialMedia;
      const result = await services.delete(`/personal-form/personal-social-media/${dataDelete.id}`)
      setConfirmationModalBox({ ...confirmationModalBox, visible: false });
      successNotify(result.message);
      setIsLoading(false);
      fetchData();
    } catch (error) {
      console.error(error)
      setConfirmationModalBox({ ...confirmationModalBox, visible: false });
      errorNotify(error.response.status + ' ' + error.response.statusText);
      setIsLoading(false);
    }
  }

  const handleCancel = () => {
    setFormActive(false);
    customizeFormData(originalJsonForm);
    setSocialMedia(null);
  }

  const handleModifyCard = (index, typeModify) => {
    if(typeModify === "edit"){
      setSocialMedia(socialMedias[index]);
      mappingFormwithData(index);
      setFormActive(true);
    } else if(typeModify === "delete") {
      setSocialMedia(socialMedias[index]);
      setConfirmationModalBox({ visible: true, mode: "Delete" });
    }
  }

  const mappingFormwithData = (index?:any) => {
    const newFormData = formData;
    newFormData.map(field => {
      if(field.field_name == "is_dont_have"){
        field.is_visible = isVisible;
      }
      if(index != null){
        if(socialMedias[index]){
          if(socialMedias[index].is_dont_have && !["id", "is_dont_have"].includes(field.field_name)){
            field.value = "";
            if(field.form_type == "checkbox"){
              field.value = false;
            }
          } else {
            field.value = socialMedias[index][field.field_name];
          }
          field.isDisabled = socialMedias[index].is_dont_have;
          field.is_mandatory = (socialMedias[index].is_dont_have) ? false : field.is_mandatory;
        }
      } else {
        if(field.form_type == "checkbox"){
          field.value = false;
        } else {
          field.value = ""
        }
      }
      return field;
    })
    setFormData(newFormData);
  }

  const customizeFormData = async (data) => {
    const form: any = data;
    form.push({
      field_name: "id",
      value: "0",
      field_type: "hidden"
    })
    const formdata = await Promise.all(form.map(async data => {
      if(data.field_name == "is_dont_have"){
        return {...data, is_visible: isVisible, value: false}
      }
      return {...data, isRequiredLabel: data.is_mandatory, isDisabled: false, isError: false};
    }))
    setFormData(formdata);
  }

  React.useEffect(() => {
    fetchData();
  }, []);

  return (
    <React.Fragment>
      <Card className={`${(socialMedias.length === 0 || !formActive) ? 'flex-1' : ''} w-full overflow-auto shadow-none rounded-none rounded-r-lg border p-6`}>
        <div className="flex justify-between">
          <Typography variant="lead" className="font-bold text-black">Social Media</Typography>
          {(!isLoading) && 
            <a 
            onClick={() => {
              let index = null;
              if(socialMedias.length > 0){
                setSocialMedia(socialMedias[0]);
                setIsVisible(socialMedias[0].is_dont_have)
                if (socialMedias[0].is_dont_have == true) {index = 0}
              } else {
                setIsVisible(1)
              }
              mappingFormwithData(index);
              setFormActive(true)
            }} 
            className='cursor-pointer font-bold text-primary-500 flex items-center gap-1'>
            <FaPlusCircle /> Social Media
            </a>
          }
        </div>
        <hr className="my-5" />
        {(isLoading) 
        ? <LoadingDiv/>
        : !errorMessage
          ? 
            <>
              {socialMedias && (socialMedias.length === 0 && !formActive) 
                ? 
                <div className="flex flex-col items-center justify-center my-auto">
                  <Typography>It seems you haven't filled out your social media yet. </Typography>
                  <Typography className="font-bold mt-3">Click Add Social Media on top right to add your social media</Typography>
                </div> 
                : socialMedias.length > 0 && !formActive
                  ? socialMedias[0].is_dont_have == false
                    ? <ListSocialMedias datas={socialMedias} actionModify={true} handleModify={handleModifyCard}/>
                    : 
                    <div className="flex flex-col items-center justify-center my-auto">
                      <Typography>You have stated that you don't have social media.</Typography>
                      <Typography className="font-bold mt-3">Click Add Social Media on top right to add your social media</Typography>
                    </div> 
                  : null
              }
              {formActive &&
                <FormSocialMedia
                  formData={formData}
                  setFormData={setFormData}
                  loading={isLoadingButton}
                  handleSubmit={handleSubmit}
                  handleCancel={handleCancel}
                />
              }
            </>
          : 
          <div className="flex flex-col items-center justify-center my-auto">
            <Typography variant="lead" className="font-bold">{errorMessage.title}</Typography>
            <Typography>{errorMessage.message}</Typography>
          </div>
        }
      </Card>
      {(socialMedias.length > 0 && socialMedias[0].is_dont_have == false && formActive) && (
        <Card className="w-full overflow-hidden shadow-none rounded-none rounded-r-lg border p-6">
          <Typography className="font-bold text-black flex items-center gap-3 mb-5"><IoDocumentTextOutline className="w-7 h-7" /> Preview Data</Typography>
          {socialMedias.length > 0
          ? <ListSocialMedias datas={socialMedias} actionModify={false} handleModify={handleModifyCard}/>
          : null}
        </Card>
      )}
      <ConfirmationModal
        modalState={confirmationModalBox}
        setModalState={setConfirmationModalBox}
        action={confirmationModalBox.mode === "Delete" ? handleDelete : async () => {}}
      />
    </React.Fragment >
  );
};

function LoadingDiv() {
  return (
    <div className="flex flex-col gap-2">
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
      <Typography as="div" variant="paragraph" className="w-full rounded-full bg-gray-100">&nbsp;</Typography>
    </div>
  )
}

type FormSocialMediaProps = {
  formData: any;
  setFormData: any;
  loading: any; 
  handleSubmit:any;
  handleCancel:any;
}

function FormSocialMedia(props:FormSocialMediaProps) {
  const [isLoading, setIsLoading] = React.useState(false);

  React.useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      try {
        await Promise.all(props.formData.map(async (object, key) => {
          if(object.field_type === "select") {
            const newData = props.formData;
            let response;
            switch (object.field_name) {
              case "social_media_id":
                response = await services.get(`/master/social_media`);
                response.data = response.data.map(data => {
                  return {label: data.name, value: data.id};
                })
              break;

              default:
                response = {data:[]};
                break;
            }
            newData[key] = {...object, options: response.data}
            props.setFormData(newData);
          }
        }))
        setIsLoading(false);
      } catch (error) {
        console.error(error);
        setIsLoading(false);
        return;
      };
    };

    fetchData();
  }, []);

  return (
    <>
      {(isLoading) && <LoadingDiv/>}
      {(!isLoading) && 
        <>
          {FormRender(props.formData, props.setFormData)}
          <div className="grid grid-col-12 mt-4">
            <div className="col-span-12 flex justify-end items-center gap-2">
              <Button variant="outlined" className="border-primary-500 text-primary-500" onClick={() => props.handleCancel()}>Cancel</Button>
              <Button 
                variant="filled" 
                className="bg-primary-500" 
                loading={props.loading} 
                onClick={() => {
                  props.handleSubmit()
                }}
              >
                {props.loading ? "Loading..." : "Save Form"}
              </Button>
            </div>
          </div>
        </>
      }
    </>
  )
}

function ListSocialMedias({datas, actionModify, handleModify}:any){
  return <ul className={``}>
    {datas.map((data, id) => {
      return (
        <li className='mt-5' key={id}>
          <CardSocialMedia index={id} data={data} actionModify={actionModify} handleModify={handleModify}/>
        </li>
      )
    })}
  </ul>
}

function CardSocialMedia({index, data, actionModify, handleModify}:any){
  const icons = (data) => {
    switch (data) {
      case 'facebook':
        return (<Facebook size={18}/>)
      case 'instagram':
        return (<Instagram size={18}/>)
      case 'linkedin':
        return (<Linkedin size={18}/>)
      case 'telegram':
        return (<Telegram size={18}/>)
      case 'tiktok':
        return (<TikTok size={18}/>)
      case 'twitter':
        return (<Twitter size={18}/>)
      default:
        return;
    };
  };
  return (
    <div className="flex flex-row items-center gap-3">
      {icons(data.icon)}
      <p className="font-bold">{data.username}</p>
      <a className="font-medium" href={data.link} target='_blank'>{data.link}</a>
      <RiPencilFill className={`w-4 h-4 cursor-pointer ${(!actionModify) ? 'hidden' : ''}`} onClick={() => handleModify(index, "edit")} />
      <RiDeleteBinFill className={`w-4 h-4 cursor-pointer ${(!actionModify) ? 'hidden' : ''}`} onClick={() => handleModify(index, "delete")} />
    </div>
  )
}