import * as React from 'react';
import { useState, useEffect } from 'react';
import { Typography, Button, IconButton, Checkbox, Input, Option, Popover, PopoverHandler, PopoverContent } from '@material-tailwind/react';
import { format, set } from "date-fns";
import { DayPicker } from "react-day-picker";
import Select from 'react-select';
import { IoIosAddCircle } from "react-icons/io";
import { IoTrash } from "react-icons/io5";
import { RiPencilFill } from "react-icons/ri";
import { IoDocumentTextOutline, IoChevronBack, IoChevronForward } from "react-icons/io5";
import { GoPerson } from "react-icons/go";
import { PiBriefcaseLight } from "react-icons/pi";
import { HiOutlineMapPin } from "react-icons/hi2";
import { MdOutlineDateRange } from "react-icons/md";
import services from '../../../../../utils/services';
import { toast } from 'react-toastify';

type T_FamilyStatus = {
  value: string,
  label: string,
}

type T_Region = {
  id: string,
  province_name: string,
}

type T_Option = {
  label: string,
  value: string,
}

type T_Field = {
  id: string | null,
  form_name: string | null,
  field_type: 'input' | 'select',
  field_name: string | null,
  form_type: 'text' | 'number' | 'date',
  max_value: number | null,
  is_visible: boolean,
  is_mandatory: boolean,
  tab_form_application_id: string,
  class: string | null,
  sort: number,
  is_other: boolean | null,
  notes: string | null,
  tooltip: string | null,
  placeholder: string | null,
  tab_name: string | null,
  is_privacy: boolean | null,
  max_upload: number | null,
  mimes: string | null,
  options: T_Option[] | null,
}

export default function Family() {
  const [formOpen, setFormOpen] = useState({open: false, status: 'add'});
  const [formDisabled, setFormDisabled] = useState<boolean>(false);
  const [familyData, setFamilyData] = useState([]);
  const [regionDataList, setRegionDataList] = useState<T_Region[]>([]);
  const [selectedData, setSelectedData] = useState<string>(null);
  const [fields, setFields] = useState<T_Field[]>([]);
  const [renderFields, setRenderFields] = useState<T_Field[]>([]);
  const [inputs, setInputs] = useState({});
  const [familyStatus, setFamilyStatus] = useState<T_FamilyStatus[]>([]);
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    getPersonalFamilyData();
    getFormId();
  }, [])

  useEffect(() => {
    fields.forEach((item) => {
      if(item.field_type === 'select' && item.form_type !== 'date') {
        if(item.field_name === 'region_id') {
          setRenderFields((prev) => prev.map(item2 => item2.id === item.id ? ({...item2, options: regionDataList.map(item3 => ({value: item3.id, label: item3.province_name}))}) : item2))
        } else {
          getFieldOptions(item.id);
        }
        // getFieldOptions(item.id);
      }
    });
  }, [fields, regionDataList])
  
  // useEffect(() => {
  //   console.log(inputs);
  // }, [inputs])

  const getFormId = async () => {
    try {
      setLoading(true);
      const response = await services.get('/master/tab-form');

      if(response.statusCode === 200) {
        const form = response.data.find((item) => item.name === 'Family');
        getFormFields(form.id);
        setLoading(false);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to get form ID data');
    }
  }

  const getFormFields = async (id: string) => {
    try {
      setLoading(true);
      const response = await services.get(`/master/form-application/${id}`);
      
      if(response.statusCode === 200) {
        getRegionData();
        setFields(response.data);
        setRenderFields(response.data);
        setLoading(false);
        // console.log(response.data);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to get form fields data');
    }
  }

  const getFieldOptions = async (id: string) => {
    try {
      setLoading(true);
      const response = await services.get(`/master/option/${id}`);

      if(response.statusCode === 200) {
        // console.log(response.data);
        setFamilyStatus(response.data);
        setRenderFields((prev) => prev.map(item => item.id === id ? ({...item, options: response.data}) : item));
        setLoading(false);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to get form field options data');
    }
  }

  const getRegionData = async () => {
    try {
      setLoading(true);
      const response = await services.get('/personal-form/master_option_region');

      if(response.statusCode === 200) {
        setRegionDataList(response.data);
        setLoading(false);
        // console.log(response);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to get region data');
    }
  }

  const getPersonalFamilyData = async () => {
    try {
      setLoading(true);
      const response = await services.get('/personal-form/personal-family/list');

      if(response.statusCode === 200) {
        setFamilyData(response.dataFamily);
        setLoading(false);
        // console.log(response);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to get personal Family data');
    }
  }

  const onSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    try {
      setLoading(true);
      if(formOpen.status === 'add') {
        const response = await services.post('/personal-form/personal-family/save', {
          ...inputs,
        });

        if(response.statusCode === 201) {
          setInputs({});
          toast.success('New personal Family data added successfully');
          setFormOpen(prev => ({...prev, open: false}));
          getPersonalFamilyData();
        }
      }
  
      if(formOpen.status === 'edit') {
        const response = await services.post(`/personal-form/personal-family/update/${selectedData}`, {
          ...inputs,
        });

        if(response.statusCode === 200) {
          setInputs({});
          toast.success('Personal Family data edited successfully');
          setFormOpen(prev => ({...prev, open: false}));
          getPersonalFamilyData();
        }
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to add personal Family data');
    }
  }

  const onEdit = (item) => {
    setInputs(item);
    setSelectedData(item.id);
    setFormOpen({open: true, status: 'edit'});
  }

  const onDelete = async (id) => {
    try {
      setLoading(true);
      const response = await services.delete(`/personal-form/personal-family/delete/${id}`);

      if(response.statusCode === 200) {
        toast.success('Personal Family data deleted successfully');
        getPersonalFamilyData();
        setLoading(false);
      }
    } catch (error) {
      console.log(error.message);
      setLoading(false);
      toast.error('Failed to delete personal Family data');
    }
  }

  const onRenderField = (item: T_Field) => {
    switch(item.field_type) {
      case 'input':
        return (
          <div key={item.id} className={`flex flex-col flex-1 gap-y-2 ${item.class}`}>
            <Typography className="text-black font-normal">{item.form_name} {item.is_mandatory ? <span className='text-red-500'>*</span> : ''}</Typography>
            <Input
              size="lg"
              color="blue"
              type={item.form_type}
              placeholder={item.placeholder}
              required={item.is_mandatory}
              className="!border !border-gray-300 bg-white text-gray-900 ring-4 ring-transparent placeholder:text-gray-500 placeholder:opacity-100 focus:!border-primary-500 focus:!border-t-primary-500"
              labelProps={{
                className: "hidden",
              }}
              value={inputs[item.field_name] || ''}
              onChange={(e) => setInputs((prev) => ({...prev, [item.field_name]: e.target.value}))}
              disabled={formDisabled || loading}
            />
          </div>
        )
      case 'select':
        return (
          <div key={item.id} className={`flex flex-col flex-1 gap-y-2 ${item.class}`}>
            <Typography className="text-black font-normal">{item.form_name} {item.is_mandatory ? <span className='text-red-500'>*</span> : ''}</Typography>
            {item.form_type !== 'date'
            ? <Select
                placeholder={item.placeholder}
                required={item.is_mandatory}
                isDisabled={formDisabled || loading}
                components={{
                  IndicatorSeparator: () => null
                }}
                options={item.field_name === 'status'
                  ? [...item.options].filter((item1) => !familyData.find((item2) => item2.status === item1.value))
                  : item.options
                }
                defaultValue={formOpen.status === 'edit' && {value: inputs[item.field_name] || '', label: item.options.find((item1) => item1.value === inputs[item.field_name])?.label || ''}}
                onChange={(e) => setInputs((prev) => ({...prev, [item.field_name]: e.value}))}
              />
            : <div className="w-full">
                <Popover placement="bottom">
                  <PopoverHandler>
                    <Input
                      labelProps={{ className: "hidden" }}
                      size="lg"
                      id="from_date"
                      type="text"
                      placeholder="From date"
                      className="!border !border-gray-300 bg-white text-gray-900 ring-4 ring-transparent placeholder:text-gray-500 placeholder:opacity-100 focus:!border-primary-500 focus:!border-t-primary-500"
                      containerProps={{ className: "min-w-[8px] w-full" }}
                      required={item.is_mandatory}
                      value={inputs[item.field_name] || ''}
                      onChange={(e) => setInputs((prev) => ({...prev, [item.field_name]: e.target.value}))}
                      disabled={formDisabled || loading}
                    />
                  </PopoverHandler>
                  <PopoverContent>
                    <DayPicker
                      mode="single"
                      className="border-0"
                      classNames={{
                        caption: "flex justify-center py-2 mb-4 relative items-center",
                        caption_label: "text-sm font-medium text-gray-900",
                        nav: "flex items-center",
                        nav_button:
                          "h-6 w-6 bg-transparent hover:bg-blue-gray-50 p-1 rounded-md transition-colors duration-300",
                        nav_button_previous: "absolute left-1.5",
                        nav_button_next: "absolute right-1.5",
                        table: "w-full border-collapse",
                        head_row: "flex font-medium text-gray-900",
                        head_cell: "m-0.5 w-9 font-normal text-sm",
                        row: "flex w-full mt-2",
                        cell: "text-gray-600 rounded-md h-9 w-9 text-center text-sm p-0 m-0.5 relative [&:has([aria-selected].day-range-end)]:rounded-r-md [&:has([aria-selected].day-outside)]:bg-gray-900/20 [&:has([aria-selected].day-outside)]:text-white [&:has([aria-selected])]:bg-gray-900/50 first:[&:has([aria-selected])]:rounded-l-md last:[&:has([aria-selected])]:rounded-r-md focus-within:relative focus-within:z-20",
                        day: "h-9 w-9 p-0 font-normal",
                        day_range_end: "day-range-end",
                        day_selected:
                          "rounded-md bg-gray-900 text-white hover:bg-gray-900 hover:text-white focus:bg-gray-900 focus:text-white",
                        day_today: "rounded-md bg-gray-200 text-gray-900",
                        day_outside:
                          "day-outside text-gray-500 opacity-50 aria-selected:bg-gray-500 aria-selected:text-gray-900 aria-selected:bg-opacity-10",
                        day_disabled: "text-gray-500 opacity-50",
                        day_hidden: "invisible",
                      }}
                      components={{
                        IconLeft: ({ ...props }) => (
                          <IoChevronBack {...props} className="h-4 w-4 stroke-2" />
                        ),
                        IconRight: ({ ...props }) => (
                          <IoChevronForward {...props} className="h-4 w-4 stroke-2" />
                        ),
                      }}
                    />
                  </PopoverContent>
                </Popover>
              </div>
            }
          </div>
        )
      default:
        return (<></>)
    }
  }

  return (
    <>
      <div className='flex flex-col h-full p-6 gap-y-5 bg-white rounded-r-lg'>
        <div className='flex gap-4 items-center justify-between'>
          <Typography variant="lead" className="font-bold text-black">Family</Typography>
          {!formOpen.open && <Button
            variant="text"
            color="blue"
            className="flex p-0 gap-1 text-primary-500 normal-case items-center hover:bg-transparent hover:text-primary-300"
            onClick={() => setFormOpen({open: true, status: 'add'})}
            disabled={loading}
            loading={loading}
          >
            <IoIosAddCircle size={20}/>
            Add Family
          </Button>}
        </div>
        <hr/>

        {formOpen.open && <div className='flex flex-col gap-y-5'>
          {!familyData.length && <Checkbox
            color="blue"
            label="I Don't have any achievement"
            checked={formDisabled}
            onChange={() => setFormDisabled(prev => !prev)}
          />}

          <form className='grid grid-cols-12 gap-6' onSubmit={onSubmit}>
            {renderFields.map((item) => onRenderField(item))}

            <div className='flex gap-2 items-center justify-end col-span-12'>
              <Button
                variant="outlined"
                color="blue"
                className="border-primary-500 text-primary-500 normal-case"
                onClick={() => {
                  setFormOpen((prev) => ({...prev, open: false}))
                  setInputs({});
                }}
                disabled={loading}
              >
                Cancel
              </Button>
              <Button
                variant="filled"
                color="blue"
                className="bg-primary-500 normal-case shadow-none hover:shadow-none hover:bg-primary-400"
                type="submit"
                onClick={onSubmit}
                disabled={loading}
                loading={loading}
              >
                {formOpen.status === "add" ? "Add Achievement" : "Edit Achievement"}
              </Button>
            </div>
          </form>
        </div>}
        
        <div className='flex flex-col h-full gap-y-1'>
          {!formOpen.open && (loading
          ? <>
            <div className='flex gap-x-4'>
              <div className='flex flex-col gap-y-1 items-center'>
                <div className='w-[20px] h-[20px] bg-gray-200 rounded-full animate-pulse'/>
                <div className={`flex-1 border-r-2 border-dotted border-gray-200`}/>
              </div>
              <div className='flex flex-col pb-2 gap-y-2'>
                <Typography className="h-[26px] w-[150px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                <Typography className="h-[20px] w-[150px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                <div className='flex gap-x-4 items-center'>
                  <Typography className="h-[20px] w-[70px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                  <Typography className="h-[20px] w-[100px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                </div>
              </div>
            </div>
            <div className='flex gap-x-4'>
              <div className='flex flex-col gap-y-1 items-center'>
                <div className='w-[20px] h-[20px] bg-gray-200 rounded-full animate-pulse'/>
                <div className={`flex-1 border-r-2 border-dotted border-gray-200`}/>
              </div>
              <div className='flex flex-col pb-2 gap-y-2'>
                <Typography className="h-[26px] w-[150px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                <Typography className="h-[20px] w-[150px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                <div className='flex gap-x-4 items-center'>
                  <Typography className="h-[20px] w-[70px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                  <Typography className="h-[20px] w-[100px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                </div>
              </div>
            </div>
            <div className='flex gap-x-4'>
              <div className='flex flex-col gap-y-1 items-center'>
                <div className='w-[20px] h-[20px] bg-gray-200 rounded-full animate-pulse'/>
                <div className={`flex-1 border-0 border-dotted border-gray-200`}/>
              </div>
              <div className='flex flex-col pb-2 gap-y-2'>
                <Typography className="h-[26px] w-[150px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                <Typography className="h-[20px] w-[150px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                <div className='flex gap-x-4 items-center'>
                  <Typography className="h-[20px] w-[70px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                  <Typography className="h-[20px] w-[100px] bg-gray-200 rounded-md animate-pulse">&nbsp;</Typography>
                </div>
              </div>
            </div>
          </>
          : familyData.length
            ? familyData.map((item, i) => (
              <div key={i} className='flex gap-x-4'>
                <div className='flex flex-col gap-y-1 items-center'>
                  <div className='w-[20px] h-[20px] border border-primary-500 bg-primary-50 rounded-full'/>
                  <div className={`flex-1 ${i >= familyData.length - 1 ? "border-0" : "border-r-2"} border-dotted border-primary-500`}/>
                </div>
                <div className='flex flex-col pb-2 gap-y-2'>
                  <div className='flex gap-x-1 items-center'>
                    <Typography className="mr-1 text-primary-900 font-bold">{familyStatus.length ? familyStatus.find((item2) => item2.value === item.status).label : item.status}</Typography>
                    <IconButton
                      variant="text"
                      className="p-0 w-5 h-5 hover:bg-transparent"
                      onClick={() => onEdit(item)}
                      disabled={loading}
                    >
                      <RiPencilFill size={20} className='text-blue-gray-800 hover:text-blue-gray-600'/>
                    </IconButton>
                    <IconButton
                      variant="text"
                      className="p-0 w-5 h-5 hover:bg-transparent"
                      onClick={() => onDelete(item.name)}
                      disabled={loading}
                    >
                      <IoTrash size={20} className='text-blue-gray-800 hover:text-blue-gray-600'/>
                    </IconButton>
                  </div>
                  <div className='flex gap-x-1 items-center'>
                    <GoPerson size={20} className='text-gray-400'/>
                    <Typography className="text-black font-normal text-sm">{item.name}</Typography>
                  </div>
                  <div className='flex gap-x-4 items-center'>
                    <div className='flex gap-x-1 items-center'>
                      <PiBriefcaseLight size={20} className='text-gray-400'/>
                      <Typography className="text-black font-normal text-sm">{item.work_status}</Typography>
                    </div>
                    <div className='flex gap-x-1 items-center'>
                      <HiOutlineMapPin size={20} className='text-gray-400'/>
                      <Typography className="text-black font-normal text-sm">{item.province_name}</Typography>
                    </div>
                  </div>
                </div>
              </div>
            ))
            : <div className='flex flex-col w-full h-full gap-4 items-center justify-center'>
              <Typography className="text-black font-normal text-center">It seems you haven't filled out your family yet.</Typography>
              <Typography variant="h5" className="text-black font-bold text-center">Click Add Family on top right to add your family</Typography>
            </div>
          )}
        </div>
      </div>

      {formOpen.open && familyData.length > 0 && <div className='flex flex-col h-full p-6 mt-4 gap-y-5 bg-white rounded-r-lg'>
        <Typography variant="lead" className="flex gap-x-2 font-bold text-black items-center"><IoDocumentTextOutline size={30}/> Preview Data</Typography>
        {familyData.map((item, i) => (
          <div key={i} className='flex flex-col pb-2 gap-y-2'>
            <Typography className="mr-1 text-primary-900 font-bold">{familyStatus.length ? familyStatus.find((item2) => item2.value === item.status).label : item.status}</Typography>
            <div className='flex gap-x-1 items-center'>
              <GoPerson size={20} className='text-gray-400'/>
              <Typography className="text-black font-normal text-sm">{item.name}</Typography>
            </div>
            <div className='flex gap-x-4 items-center'>
              <div className='flex gap-x-1 items-center'>
                <PiBriefcaseLight size={20} className='text-gray-400'/>
                <Typography className="text-black font-normal text-sm">{item.work_status}</Typography>
              </div>
              <div className='flex gap-x-1 items-center'>
                <HiOutlineMapPin size={20} className='text-gray-400'/>
                <Typography className="text-black font-normal text-sm">{item.province_name}</Typography>
              </div>
            </div>
          </div>
        ))}
      </div>}
    </>
  )
};