import * as React from 'react';
import { Input, Checkbox, Popover, PopoverHandler, PopoverContent, Textarea } from '@material-tailwind/react';
import { isIsoDate } from "../utils/helper";
import { DayPicker } from 'react-day-picker';
import { IoChevronBack, IoChevronForward, IoCalendarOutline } from 'react-icons/io5';
import { format, parseISO } from 'date-fns';
import ReactSelect from 'react-select';

export default function FormRender(jsonForm:any, setFormData:any) {
    const dangerStyles = (id) =>{
        return {
        control: (provided, state) => ({
            ...provided,
            border: jsonForm[id].isError ? '2px solid #ef4444' : state.isFocused ? '2px solid #1566D1' : '2px solid #d1d5db',
            borderRadius: '0.375rem', // Adjust border radius as needed
            height: '44px',
            minHeight: '44px',
        }),
        placeholder: (provided) => ({
            ...provided,
            color: '#9ca3af', // Adjust color as needed
        }),
        }
    };

    return (
        <div className="grid grid-cols-12 gap-x-3">
        {jsonForm.map((field, idx) => {
            switch (field.field_type) {
            case "input":
                if(["text", "number"].includes(field.form_type)){
                    return (
                        <div className={`mt-6 ${field.class}`} key={idx}>
                            <label htmlFor={field.field_name} className={`font-semibold`}>{field.form_name} {(field.isRequiredLabel === 1) && <span className="text-danger-500">*</span>}</label>
                            <Input
                                labelProps={{ className: "hidden" }}
                                size="lg"
                                id={field.field_name}
                                type={field.form_type}
                                placeholder={field.placeholder}
                                className={`!border-1 ${field.isError ? '!border-danger-500' : '!border-gray-300'} placeholder:text-gray-400 placeholder:opacity-100 focus:!outline-none focus:!border-primary-500 mt-1`}
                                onChange={({ target }) => setFormData([...jsonForm].map(object => {
                                    if(object.field_name === field.field_name) {
                                    return {
                                        ...object,
                                        value: target.value,
                                        isError: (field.is_mandatory == 1) ? ((target.value != "") ? false : true) : false
                                    }
                                    }
                                    else return object;
                                }))}
                                value={field.value}
                                disabled={field.isDisabled}
                            />
                        </div>
                    )
                } 
                if(field.form_type == "date"){
                    return (
                        <div className={`mt-6 ${field.class}`} key={idx}>
                            <label htmlFor={field.field_name} className={`font-semibold`}>{field.form_name} {(field.isRequiredLabel === 1) && <span className="text-danger-500">*</span>}</label>
                            <Popover placement="bottom">
                                <PopoverHandler>
                                <Input
                                    labelProps={{ className: "hidden" }}
                                    size="lg"
                                    id={field.field_name}
                                    type="text"
                                    placeholder="From date"
                                    icon={<IoCalendarOutline className="h-7 w-7 text-gray-300"/>}
                                    className={`!border-1 ${field.isError ? '!border-danger-500' : '!border-gray-300'} placeholder:text-gray-400 placeholder:opacity-100 focus:!outline-none focus:!border-primary-500 mt-1`}
                                    containerProps={{ className: "min-w-[8px]" }}
                                    onChange={() => null}
                                    value={field.value ? (isIsoDate(field.value)) ? format(parseISO(field.value.split('T')[0]), 'dd-MM-yyyy') : format(field.value, 'dd-MM-yyyy') : ''}
                                    disabled={field.isDisabled}
                                />
                                </PopoverHandler>
                                <PopoverContent>
                                <DayPicker
                                    mode="single"
                                    selected={(field.value) ? (isIsoDate(field.value)) ? parseISO(field.value.split('T')[0]) : field.value : ''}
                                    defaultMonth={(field.value) ? (isIsoDate(field.value)) ? parseISO(field.value.split('T')[0]) : field.value : new Date()}
                                    onSelect={(date) => setFormData([...jsonForm].map(object => {
                                    if(object.field_name === field.field_name) {
                                        return {
                                            ...object,
                                            value: date ? date : '',
                                            isError: (field.is_mandatory == 1) ? ((date) ? false : true) : false
                                        }
                                    }
                                    else return object;
                                    }))}
                                    showOutsideDays
                                    className="border-0"
                                    classNames={{
                                    caption: "flex justify-center py-2 mb-4 relative items-center",
                                    caption_label: "text-sm font-medium text-gray-900",
                                    nav: "flex items-center",
                                    nav_button:
                                        "h-6 w-6 bg-transparent hover:bg-blue-gray-50 p-1 rounded-md transition-colors duration-300",
                                    nav_button_previous: "absolute left-1.5",
                                    nav_button_next: "absolute right-1.5",
                                    table: "w-full border-collapse",
                                    head_row: "flex font-medium text-gray-900",
                                    head_cell: "m-0.5 w-9 font-normal text-sm",
                                    row: "flex w-full mt-2",
                                    cell: "text-gray-600 rounded-md h-9 w-9 text-center text-sm p-0 m-0.5 relative [&:has([aria-selected].day-range-end)]:rounded-r-md [&:has([aria-selected].day-outside)]:bg-gray-900/20 [&:has([aria-selected].day-outside)]:text-white [&:has([aria-selected])]:bg-gray-900/50 first:[&:has([aria-selected])]:rounded-l-md last:[&:has([aria-selected])]:rounded-r-md focus-within:relative focus-within:z-20",
                                    day: "h-9 w-9 p-0 font-normal",
                                    day_range_end: "day-range-end",
                                    day_selected:
                                        "rounded-md bg-gray-900 text-white hover:bg-gray-900 hover:text-white focus:bg-gray-900 focus:text-white",
                                    day_today: "rounded-md bg-gray-200 text-gray-900",
                                    day_outside:
                                        "day-outside text-gray-500 opacity-50 aria-selected:bg-gray-500 aria-selected:text-gray-900 aria-selected:bg-opacity-10",
                                    day_disabled: "text-gray-500 opacity-50",
                                    day_hidden: "invisible",
                                    }}
                                    components={{
                                    IconLeft: ({ ...props }) => (
                                        <IoChevronBack {...props} className="h-4 w-4 stroke-2" />
                                    ),
                                    IconRight: ({ ...props }) => (
                                        <IoChevronForward {...props} className="h-4 w-4 stroke-2" />
                                    ),
                                    }}
                                />
                                </PopoverContent>
                            </Popover>
                        </div>
                    )
                }
                if(field.form_type == "checkbox"){
                    return (
                        <div key={idx} className={`-ml-3 ${field.class} ${(field.is_visible || field.field_name != "is_dont_have") ? "" : "hidden"}`}>
                            <Checkbox
                            label={<div className="flex items-center gap-2">{field.form_name}</div>}
                            checked={(field.value != null ? field.value : false)}
                            onChange={(e) => {
                                let newJsonForm;
                                if(field.field_name === "is_dont_have"){
                                    if(e.target.checked){
                                        newJsonForm = jsonForm.map((field) => {
                                            if(field.field_name === "is_dont_have"){
                                                return {...field, value: e.target.checked}
                                            }
                                            if(field.field_name === "id"){
                                                return {...field};
                                            }
                                            if(field.form_type === "checkbox"){
                                                return {...field, value: false, is_mandatory: field.isRequiredLabel, isError: false, isDisabled: false}
                                            }
                                            return {...field, value: "", is_mandatory: 0, isError: false, isDisabled: true}
                                        })
                                    } else {                    
                                        newJsonForm = jsonForm.map((field) => {
                                            if(field.field_name === "is_dont_have"){
                                                return {...field, value: e.target.checked}
                                            }
                                            if(field.field_name === "id"){
                                                return {...field};
                                            }
                                            if(field.form_type === "checkbox"){
                                                return {...field, value: false, is_mandatory: field.isRequiredLabel, isError: false, isDisabled: false}
                                            }
                                            return {...field, value: "", is_mandatory: field.isRequiredLabel, isError: false, isDisabled: false}
                                        })
                                    }
                                } else if(["is_current", "is_lifetime"].includes(field.field_name)) {
                                    if(e.target.checked){
                                        newJsonForm = jsonForm.map((object) => {
                                            if(object.field_name === field.field_name){
                                                return {...object, value: e.target.checked}
                                            }
                                            if(object.field_name === "end_date"){
                                                return {...object, value: field.form_name, form_type: "text", isDisabled: true}
                                            }
                                            return object
                                        })
                                    } else {                    
                                        newJsonForm = jsonForm.map((object) => {
                                            if(object.field_name === field.field_name){
                                                return {...object, value: e.target.checked}
                                            }
                                            if(object.field_name === "end_date"){
                                                return {...object, value: "", form_type: "date", isDisabled: false}
                                            }
                                            return object
                                        })
                                    }
                                } else {
                                    newJsonForm = jsonForm.map((item) => {
                                        if(field.field_name === item.field_name){
                                            return {...field, value: e.target.checked}
                                        }
                                        return {...item}
                                    });
                                }
                                setFormData(newJsonForm)
                            }}
                            /> 
                        </div>
                    )
                }
            case "textarea":
                return (
                    <div className={`mt-6 ${field.class}`} key={idx}>
                        <label htmlFor={field.field_name} className={`font-semibold`}>{field.form_name} {(field.isRequiredLabel === 1) && <span className="text-danger-500">*</span>}</label>
                        <Textarea
                        labelProps={{ className: "hidden" }}
                        id={field.field_name}
                        placeholder={field.placeholder}
                        className={`!border-1 ${field.isError ? '!border-danger-500' : '!border-gray-300'} placeholder:text-gray-400 placeholder:opacity-100 focus:!outline-none focus:!border-primary-500 mt-1`}
                        onChange={({ target }) => setFormData([...jsonForm].map(object => {
                            if(object.field_name === field.field_name) {
                                return {
                                    ...object,
                                    value: target.value,
                                    isError: (field.is_mandatory == 1) ? ((target.value != "") ? false : true) : false
                                }
                            }
                            else return object;
                        }))}
                        value={field.value}
                        disabled={field.isDisabled}
                        >{field.value}</Textarea>
                    </div>
                )
            case "select":
                return (
                    <div key={idx} className={`mt-6 ${field.class} ${(field.is_visible == 0) ? "hidden" : ""}`}>
                        <label htmlFor={field.field_name} className={`font-semibold`}>{field.form_name} {(field.isRequiredLabel === 1) && <span className="text-danger-500">*</span>}</label>
                        <ReactSelect
                            id={field.field_name}
                            placeholder={field.placeholder}
                            className={`mt-1`}
                            styles={dangerStyles(idx)}
                            components={{
                            IndicatorSeparator: () => null
                            }}
                            options={field.options}
                            onChange={(e) => {
                                setFormData([...jsonForm].map(object => {
                                    if(object.field_name === field.field_name) {
                                        return {
                                            ...object,
                                            value: e.value,
                                            isError: (field.is_mandatory == 1) ? ((e.value != "") ? false : true) : false
                                        }
                                    }
                                    else return object;
                                }))

                                if(field.field_name == "location_type"){
                                    let newJsonForm;
                                    if(e.value == "offline"){
                                        newJsonForm = jsonForm.map((object) => {
                                            if(object.field_name === "location_id"){
                                                return {...object, is_visible: 1, value: ""}
                                            }
                                            if(object.field_name === field.field_name){
                                                return {
                                                    ...object,
                                                    value: e.value,
                                                    isError: (field.is_mandatory == 1) ? ((e.value != "") ? false : true) : false
                                                }
                                            }
                                            return object;
                                        })
                                    } else {                    
                                        newJsonForm = jsonForm.map((object) => {
                                            if(object.field_name === "location_id"){
                                                return {...object, is_visible: 0, value: ""}
                                            }
                                            if(object.field_name === field.field_name){
                                                return {
                                                    ...object,
                                                    value: e.value,
                                                    isError: (field.is_mandatory == 1) ? ((e.value != "") ? false : true) : false
                                                }
                                            }
                                            return object;
                                        })
                                    }
                                    setFormData(newJsonForm)
                                }
                            }}
                            value={(field.options) ? field.options.find(item => item.value === field.value) : ''}
                            isDisabled={field.isDisabled}
                        />
                    </div>
                )
            default:
                break;
            }
        })}
        </div>
    )
}